<template>
  <div
    class="price-field flex-panel shadow-normal"
    :class="{ 'is-focus': isFocus }"
  >
    <!--    <label for="priceEl"
               @touchstart="onFocus"
               @mousedown="onFocus">
        </label>-->
    <span class="label">金额</span>
    <input
      :disabled="disabled"
      id="priceEl"
      autocomplete="off"
      type="text"
      @focus="inputFocus"
      class="price pre-input-border"
      v-model="vValue"
      ref="input"
      tabindex="1"
    />
    <div class="input-border"></div>
  </div>
</template>

<script>
export default {
  name: "PriceField",
  props: {
    disabled: Boolean,
    value: [String, Number],
    isFocus: Boolean,
    max: {
      type: Number,
      default: 99999.99,
    },
    precision: {
      type: Number,
      default: 2,
    },
  },
  components: {},
  data() {
    return {};
  },
  created() {},
  methods: {
    onFocus(e) {
      if (this.disabled) return;
      console.log("focus");
      const el = this.$refs["input"];
      const length = (this.value + "").length;
      el.setSelectionRange(length, length);
    },
    getFocusIndex() {
      return (this.__focusIndex__ = this.$refs?.input?.selectionStart ?? 0);
    },
    inputFocus() {
      if (this.disabled) return;
      this.focus();
    },
    focus() {
      this.$emit("focus", { el: this });
      this.__focusIndex__ = this.vValue.length;
    },

    setFocus() {
      if (this.disabled) return;
      this.$refs?.input.focus();
      this.focus();
    },

    setBlur() {
      this.$refs?.input.blur();
    },
    setValue(v) {
      console.log("set price field value ", v);
      const input = this.$refs?.input;
      let index = this.__focusIndex__ || input.value.length;
      index += v.length - this.vValue.length;
      this.vValue = v;
      this.$nextTick(() => {
        if (!input) return;
        input.selectionStart = index;
        input.selectionEnd = index;
      });
    },
    getValue() {
      return this.vValue;
    },
  },
  computed: {
    vValue: {
      get() {
        return this.value;
      },
      set(v) {
        if (!/^\d*\.?\d{0,2}$/.test(v)) {
          this.$refs["input"].value = this.value;
          return;
        }
        let list = (v + "").split("");
        if (list[0] === "0" && list.length === 2 && !list.includes(".")) {
          list = (parseInt(v + "") + "").split("");
        }
        if (list.length && list.every((i) => i === "0")) {
          list = ["0"];
        }
        if (list.indexOf(".") > 0) {
          list = list.slice(0, list.indexOf(".") + (this.precision + 1));
        }

        if (+list.join("") > this.max) {
          this.$refs["input"].value = this.value;
          return;
        }

        this.$refs["input"].value = list.join("");

        this.$emit("input", list.join(""));
      },
    },
  },
};
</script>

<style lang="less" rel="stylesheet/less">
.price-field {
  background-color: #fff;
  font-size: @font-size-default;
  border-radius: @border-radius-middle;
  height: 72px;
  padding: 0 22px;
  position: relative;
  overflow: hidden;

  &.is-focus {
    .input-border {
      border-color: @active-color;
    }
  }

  .label {
    color: @text-color-black;
    width: 2.5em;
    white-space: nowrap;
  }

  label {
    position: absolute;
    bottom: 0;
    right: 0;
    top: 0;
    left: 0;
  }

  .price {
    flex: 1;
    width: 300px;
    border: 0;
    text-align: right;
    font-size: @font-size-huge;
    -webkit-appearance: none;
  }
}
</style>
