<template>
  <el-popover class="cash-field" v-model="show" width="350">
    <div class="panel">
      <span class="label">还需支付:</span>
      <span class="price">{{ restMoney | fixed }}</span>
    </div>
    <div class="panel">
      <span class="label">现金支付:</span>
      <span class="value">
        <el-input-number
          :controls="false"
          v-model="paidCash"
          ref="input"
          @focus="focus"
        />
      </span>
    </div>
    <div class="panel">
      <span class="label">找零:</span>
      <span class="value pay-back">
        <el-input-number
          :controls="false"
          :max="moneyGap"
          v-model="payBack"
          @focus="focus"
        />
      </span>
    </div>
    <div class="panel">
      <div
        style="cursor: pointer"
        @click="savingValue = !savingValue"
        :class="savingValue ? 'text-blue' : 'text-gray'"
      >
        <i class="el-icon-success"></i>
        <span
          >另外<b>{{ changeMoney | fixed }}</b
          >元存入会员储值中</span
        >
      </div>
    </div>
  </el-popover>
</template>

<script>
export default {
  name: "CashField",
  props: {
    show: Boolean,
    restMoney: Number,
    value: Number,
    cash: Number
  },
  components: {},
  data() {
    return {
      savingValue: false,
      paidCash: 0, // 消费者支付的现金金额
      payBack: "",
      width: document.documentElement.clientWidth * 0.375 - 16
    };
  },
  created() {},
  methods: {
    focus(e) {
      e.target.select();
    }
  },
  computed: {
    changeMoney() {
      if (!this.paidCash) return 0;
      let result = Math.subtract(this.paidCash, this.restMoney);
      result = Math.subtract(result, +this.payBack);
      if (result < 0) return 0;
      return result;
    },

    moneyGap() {
      if (!this.paidCash) return 0;
      const result = Math.subtract(this.paidCash, this.restMoney);
      if (result < 0) return 0;
      return result;
    }
  },
  watch: {
    show(now) {
      if (now) {
        this.paidCash = this.restMoney;
        this.$nextTick(() => {
          this.$refs["input"] && this.$refs["input"].focus();
        });
      } else {
        this.paidCash = 0;
      }
    },
    paidCash(now) {
      console.log("show cash");
      this.payBack = Math.subtract(now, +this.restMoney);

      if (this.savingValue) {
        this.$emit("input", this.changeMoney);
        this.$emit("update:changeMoney", 0);
      } else {
        this.$emit("update:changeMoney", this.changeMoney);
      }
      this.$emit("update:cash", Math.subtract(now, +this.payBack));
    },
    savingValue(now) {
      if (now) {
        this.payBack = 0
        this.$emit("input", this.changeMoney);
        this.$emit("update:changeMoney", 0);
      } else {
        this.$emit("update:changeMoney", this.changeMoney);
        this.$emit("input", 0);
      }
    }
  }
};
</script>

<style lang="less" rel="stylesheet/less">
.cash-field {
  .el-popover {
    transform: translate(0, calc(-100% - 12px));
  }

  .panel {
    text-align: left;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    font-size: 15px;

    .label {
      margin-right: 10px;
    }

    b {
      font-weight: bold;
      color: #ff6400;
    }

    .price {
      color: #ff6400;
      font-size: 30px;

      &:before {
        content: "￥";
        font-size: 15px;
      }
    }

    i {
      margin-right: 5px;
    }

    .text-gray {
      color: #999;
    }

    .text-ble {
      color: #8ab9ff;
    }
  }

  .pay-back {
  }

  .el-input {
    input {
      text-align: right;
      font-size: 28px;
    }
  }
}
</style>
