<template>
  <div
    class="property-validator"
    :class="{ hidden: !show, 'is-focus': isFocus }"
  >
    <div aspectratio w-795-100>
      <div aspectratio-content class="flex-panel">
        <div class="input-wrap">
          <span class="prepend">会员密码</span>
          <div class="input">
            <input
              maxlength="6"
              type="password"
              v-model="vValue"
              class="pre-input-border"
              autocomplete="new-password"
              placeholder="请输入密码"
              :disabled="disabled"
              tabindex="3"
              @focus="inputFocus"
              ref="input"
            />
          </div>
          <div class="input-border"></div>
        </div>
        <button
          class="code-btn"
          :disabled="disabled"
          v-if="mode === 2"
          @click="getVerifyCode"
        >
          动态密码
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import * as API from "api";

export default {
  name: "PropertyValidator",
  props: {
    value: String,
    disabled: Boolean,
    show: Boolean,
    isFocus: Boolean
  },
  components: {},
  data() {
    return {};
  },
  created() {},
  methods: {
    async getVerifyCode() {
      const result = await API.getVerifyCode(this.memberGuid);
      if (result.success) {
        this.$message({
          type: "success",
          message: "已发送动态密码!"
        });
      }
      console.log(result);
    },
    getFocusIndex() {
      return (this.__focusIndex__ = this.$refs?.input?.selectionStart ?? 0);
    },
    inputFocus() {
      this.focus();
    },
    setFocus() {
      this.$refs?.input.focus();
      this.focus();
    },
    focus() {
      this.$emit("focus", { el: this });
      this.__focusIndex__ = this.vValue.length;
    },
    getValue() {
      return this.vValue;
    },
    setValue(v) {
      console.log("set price field value ", v);
      if (v.length > 6) {
        return;
      }
      const input = this.$refs?.input;
      let index = this.__focusIndex__ || input.value.length;
      index += v.length - this.vValue.length;
      this.vValue = v;
      this.$nextTick(() => {
        if (!input) return;
        input.selectionStart = index;
        input.selectionEnd = index;
      });
    }
  },
  computed: {
    mode() {
      if (!this.params) return 1;
      return this.params.payParameter.pwdMode;
    },
    memberGuid() {
      if (!this.memberData) return "";
      return this.memberData.memberGuid;
    },
    ...mapState("Main", ["memberData", "params"]),
    vValue: {
      get() {
        return this.value;
      },
      set(v) {
        this.$emit("input", v);
      }
    }
  }
};
</script>

<style lang="less" rel="stylesheet/less">
.property-validator {
  transition: 0.1s;

  &.hidden {
    opacity: 0;
    pointer-events: none;
  }

  .flex-panel {
    align-items: stretch;
  }

  .el-input {
    height: 100%;
    margin-right: 12px;

    .el-input__inner {
      height: 100%;
    }
  }

  .input-wrap {
    flex: 1;
    width: 75%;
    display: flex;
    background-color: #fff;
    margin-right: 10px;
    border: 1px solid #e5e5e5;
    overflow: hidden;
    border-radius: @border-radius-middle;
    position: relative;

    .prepend {
      white-space: nowrap;
      padding: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #e5e5e5;
    }

    .input {
      display: flex;
      align-items: center;
      justify-content: center;
      flex: 1;
    }

    input {
      width: 100%;
      outline: transparent;
      padding: 10px;
      border: 0;
    }
  }

  .code-btn {
    width: 25%;
    border: 1px solid #dcdfe6;
    background-color: #fff;
    font-size: @font-size-mini;
    color: @text-color-gray;
    border-radius: @border-radius-small;

    &:active {
      box-shadow: inset 0 0 30px rgba(0, 0, 0, 0.1);
    }
  }

  [w-795-100] {
    aspect-ratio: "795:100";
  }
}
</style>
