import Vue from "vue";
import App from "./App.vue";
import store from "./store/index";
import "./styles/index.less";
import "./components";
import "./icons/index";
import * as utils from "./utils";
import * as Element from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
import * as constant from "@/utils/constant";
// import '@/test/pay-code.test';
// import '@/test/calculator.test';
window.countTime = (fn, message = "") => {
  console.time(message);
  fn();
  console.timeEnd(message);
};

Vue.use(Element);
Vue.config.productionTip = false;
Vue.prototype.$utils = utils;
Vue.prototype.$const = constant;

Vue.filter("fixed", (v, len = 2) =>
  typeof v === "number" ? v.toFixed(len) : v
);
Vue.filter("decimal", (v) =>
  typeof v === "number" ? Math.changeToDecimal(v) : v
);
Vue.filter("ellipsis", (v, len = 8) => utils.ellipsis(len)(v));

new Vue({
  store,
  render: (h) => h(App),
}).$mount("#app");
