<template>
  <el-dialog
    :visible.sync="show"
    title="选择卡号"
    top="32vh"
    class="payment-action"
    width="400px"
    @closed="close"
  >
    <div class="payment-body">
      <h3>还需支付</h3>
      <p class="price">{{ money | fixed }}</p>
      <input
        class="payment-input"
        type="text"
        v-model="paymentCode"
        ref="input"
        @change="confirm"
      />
      <div class="payment-code" v-if="paymentCode">
        <p>{{ paymentCode | split }}</p>
      </div>
      <p class="message" v-if="!paymentCode">等待扫描付款码...</p>
      <p class="tips" v-if="step === WAIT_CHECKOUT">收款中...</p>
      <button
        class="cancel-btn"
        v-if="step !== WAIT_CHECKOUT"
        @click="onCancel"
      >
        取消
      </button>
    </div>
  </el-dialog>
</template>

<script>
import checkPaymentCode from "@/utils/pay-code-validator";
import { getPayCode, cancelPayCode } from "@/utils/ext";

export default {
  name: "PaymentAction",
  components: {},
  data() {
    return {
      show: false,
      step: 0,
      paymentCode: "",
      WAIT_SCAN: 0,
      WAIT_CHECKOUT: 1,
      COMPLETE: 2,
      enabledPayWay: {
        isWeChatPayEnabled: false,
        isAliPayEnabled: false,
        isBestPayEnabled: false,
        isUnionPayEnabled: false,
      },
    };
  },
  created() {},
  filters: {
    split: (v) => v.replace(/(\d{4})/gi, "$1  "),
  },
  async mounted() {
    await this.$utils.sleep(50);
    this.awaitGuestCode();
    this.$nextTick(() => {
      this.$refs["input"].focus();
    });
  },
  methods: {
    close() {
      this.$destroy();
    },
    confirm() {
      const result = checkPaymentCode(this.paymentCode, this.enabledPayWay);
      if (!result.success) {
        return this.resolve({
          success: false,
          data: null,
          message: result.message,
          vm: this,
        });
      }
      this.changeStep({
        paymentCode: this.paymentCode,
        thirdPayWay: result.thirdPayWay,
        paidThirdPay: this.money,
      });
    },
    onCancel() {
      this.resolve({
        success: false,
        data: null,
        vm: this,
        message: "用户取消扫码!",
      });
      // 通知父页面取消扫码,父页面通知可能存在的电子立牌退出扫码模式
      cancelPayCode();
      this.show = false;
    },
    changeStep(data) {
      this.$refs["input"].blur();
      this.resolve({ success: true, data, vm: this });
      this.step = this.WAIT_CHECKOUT;
    },

    awaitGuestCode() {
      console.log("通知客显,等待用户扫码回传支付码");
      getPayCode({ money: this.money }).then((res) => {
        if (!res.success) return;
        this.paymentCode = res.data.paymentCode;
        this.confirm();
      });
    },
  },
  computed: {},

  destroyed() {
    this.$el && this.$el.remove();
  },
};
</script>

<style lang="less" rel="stylesheet/less">
.payment-action {
  .payment-body {
    h3 {
      margin-bottom: 10px;
    }

    .price {
      color: @text-color-price;
      font-size: @font-size-great;
      font-weight: bold;
      margin-bottom: 10px;

      &:before {
        content: "￥";
        font-weight: normal;
        font-size: @font-size-default;
      }
    }

    .payment-input {
      height: 0;
      width: 0;
      padding: 0;
      border: 0;
      opacity: 0;
      position: absolute;
      top: -100vh;
    }

    .payment-code {
      display: flex;
      font-size: @font-size-normal;
      margin: 0 auto 30px;
      border-radius: @border-radius-large;
      background-color: #f0f0f0;
      border: 1px solid #999;
      color: @text-color-dark;
      height: 48px;
      width: 304px;
      align-items: center;
      justify-content: center;

      i {
        margin-left: 10px;
        color: #32c45e;
      }
    }

    .tips {
      font-size: @font-size-normal;
    }

    .message {
      font-size: @font-size-big;
      color: @text-color-dark;
      margin-bottom: 30px;
    }

    text-align: center;
  }

  .cancel-btn {
    background-color: #e7e7e7;
    width: 195px;
    height: 48px;
    border-radius: @border-radius-large;
    border: 0;
  }
}

/*region modify*/
.payment-action {
  .el-dialog {
    overflow: hidden;
    border-radius: @border-radius-middle;
  }

  .el-dialog__header {
    display: none;
  }

  .el-dialog__body {
  }
}

/*endregion*/
</style>
