import "../math";
import CashContext from "./activity";
import * as API from "api";
import Coupon from "./coupon";

function isCheckMapChange() {
  let checkMap = null;
  return function (now) {
    let flag;
    if (!checkMap) {
      flag = true;
    } else {
      flag = isSame(checkMap, now);
    }
    checkMap = { ...now };
    return flag;
  };
}

const watchCheckMap = isCheckMapChange();

class Calculator {
  constructor(params) {
    this.name = "calculator";
    this.checked = false;
    this.params = params;
    this.result = { money: 0 };
  }

  setNext(calc) {
    this.next = calc;
  }

  getDiscount() {
    return this.result.money;
  }

  getResult() {
    return this.result;
  }

  checkByMap(map) {
    this.checked = map[this.name];
  }

  getRemainingMoney() {
    return this.remainingMoney;
  }

  calc(money, unit) {
    return money;
  }

  async handle(remainingMoney, originMoney, totalUnit) {
    remainingMoney *= 1;
    originMoney *= 1;
    this.originMoney = originMoney;
    if (remainingMoney !== undefined) {
      this.remainingMoney = remainingMoney;
    }
    // console.log("剩余", remainingMoney);
    await this.calc(this.result.money, totalUnit);
    if (this.next) {
      let nextMoney = Math.subtract(this.remainingMoney, this.result.money);
      nextMoney = nextMoney > 0 ? nextMoney : 0;
      await this.next.handle(nextMoney, originMoney, totalUnit);
    }
  }
}

// 计算会员折扣
class DiscountCalculator extends Calculator {
  constructor(params) {
    super(params);
    this.name = "discount";
  }

  calc(money, unit) {
    if (this.checked && this.params) {
      // 折扣模式为按金额折扣
      if (this.params.discountMode === 0) {
        const rate = Math.subtract(1, this.params.memberDiscount);
        this.result.money = Math.fixed(Math.mult(this.remainingMoney, rate), 2);
        // console.log(`按金额折扣,共优惠${this.result.money}`);
      }
      // 按升数折扣
      else {
        this.result.money = Math.fixed(
          Math.mult(unit, this.params.discountPerLiter),
          2
        );
        // console.log(
        //   `按升数折扣,每升优惠${this.params.discountPerLiter}, ${unit}升优惠${this.result.money}`
        // );
      }
    } else {
      this.result = { money: 0 };
    }
  }
}

// 优惠活动
class ActivityCalculator extends Calculator {
  constructor(params) {
    super(params);
    this.name = "activity";
  }

  calc(money, unit) {
    if (this.params) {
      this.cashContext = CashContext.create(this.params.body, this.params.type);
    }
    if (this.checked && this.params) {
      const result = this.cashContext.getResult(
        this.params.guid,
        this.remainingMoney,
        unit
      );
      this.result.label = this.cashContext.display();
      this.result.money = Math.subtract(this.remainingMoney, result);
    } else {
      this.result = {
        money: 0,
        label: this.cashContext && this.cashContext.display(),
      };
    }
  }
}

class MultiActivityCalculator extends Calculator {
  constructor(params) {
    super(params);
    this.name = "activity";
  }

  calc(money, unit) {
    const {
      params: { list },
      checked,
      remainingMoney,
    } = this;
    console.log("复数活动计算器", remainingMoney);
    if (!list || !list.length || !checked) {
      this.result = {
        money: 0,
        label: "活动",
      };
      return;
    }

    const results = list.map((param) => {
      const calc = new ActivityCalculator(param);
      calc.remainingMoney = remainingMoney;
      calc.checked = checked;
      calc.calc(money, unit);
      return calc.result;
    });

    console.log("所有活动结果", results);

    const maxResult = results.reduce(
      (pre, it) => (pre ? (pre.money > it.money ? pre : it) : it),
      null
    );
    console.log("最优惠结果", maxResult);
    this.result = maxResult;
  }
}

const couponEnable = (item, money) =>
  (item.minConsumeValue > 0 && item.minConsumeValue <= money) ||
  item.minConsumeValue === 0;

export class CouponCalculator extends Calculator {
  constructor(params) {
    super(params);
    this.name = "coupon";
  }

  calc() {
    if (!this.params) {
      this.result.total = 0;
      this.result.count = 0;
      this.result.coupons = [];
      return;
    }
    if (!this.checked) {
      this.result.enabledCount = this.params.list.reduce(
        (p, it) => p + it.number,
        0
      );
      return;
    }
    if (this.params.pickedCoupons.length) {
      this.result = Coupon.getResult(
        this.params,
        this.remainingMoney,
        this.params.pickedCoupons
      );
    } else {
      this.result = Coupon.autoTakeCoupon(this.params, this.remainingMoney);
    }
  }
}

class PointCalculator extends Calculator {
  constructor(params) {
    super(params);
    this.name = "point";
  }

  static checkCanUsePoint(money, options) {
    if (options.memberAvailablePoint <= 0) {
      return {
        success: false,
        type: 0,
      };
    }
    if (options.memberAvailablePoint < options.canPaidPointForMemberPoint) {
      return {
        success: false,
        type: 1,
      };
    }
    if (money < options.canPaidPointForConsumeValue) {
      return {
        success: false,
        type: 2,
      };
    }
    return {
      success: true,
    };
  }

  //preChainResultMoney,上一个业务计算的结果金额
  calc(preChainResultMoney) {
    if (!this.params) return;
    const checkMoney = Math.add(this.remainingMoney, preChainResultMoney);
    const checkResult = PointCalculator.checkCanUsePoint(
      checkMoney,
      this.params
    );
    this.result.checkResult = checkResult;
    if (this.checked && checkResult.success) {
      let memberAvailableYuan =
        Math.divide(
          this.params.memberAvailablePoint,
          this.params.pointPerYuan
        ) * 1;
      let consumemoney = this.remainingMoney;
      let orderMaxYuan =
        Math.mult(consumemoney, this.params.paidMoneyMaxRate) * 1;
      let consumeyuan = 0;
      let resultyuan = 0;
      if (memberAvailableYuan < orderMaxYuan) {
        resultyuan = Math.floor(memberAvailableYuan * 100) / 100;
        consumeyuan = Math.changeToDecimal(resultyuan, 2) * 1;
      } else {
        resultyuan = Math.floor(orderMaxYuan * 100) / 100;
        consumeyuan = Math.changeToDecimal(resultyuan, 2) * 1;
      }
      var consumePoint =
        Math.changeToDecimal(
          Math.mult(consumeyuan, this.params.pointPerYuan),
          2
        ) * 1;
      this.result.money = consumeyuan;
      this.result.point = consumePoint;
      this.result.totalPoint = Math.changeToDecimal(
        this.params.memberAvailablePoint
      );
    } else {
      this.result = {
        money: 0,
        point: 0,
        totalPoint: Math.changeToDecimal(this.params.memberAvailablePoint),
      };
    }
  }
}

const dftValueOptions = {
  memberAvailableValue: 0,
};

class ValueCalculator extends Calculator {
  constructor(params, parent) {
    super(params);
    this.checkMother = false;
    this.checkValue = false;
    this.parent = parent;
    this.params = { ...dftValueOptions, ...params };
  }

  checkByMap(map) {
    this.checkMother = map.motherCard;
    this.checkValue = map.value;
  }

  // 参与计算的可用子卡储值或非子母卡时的储值
  get value() {
    if (!this.params.value) return 0;
    return Math.min(
      this.params.value.everyUseValueMaxNumber,
      this.params.value.availableValue
    );
  }

  // 参与展示的可用子卡储值或非子母卡时的储值
  get availableValue() {
    if (!this.params.value) return 0;
    return this.params.value.availableValue;
  }

  // 参与计算可用母卡储值
  get motherValue() {
    if (!this.params || !this.params.motherCard) return 0;
    const { limitValue, motherAvailableValue } = this.params.motherCard;
    return Math.min(
      motherAvailableValue,
      limitValue,
      this.params.value.everyUseValueMaxNumber
    );
  }

  // 参与展示可用母卡储值
  get motherAvailableValue() {
    if (!this.params || !this.params.motherCard) return 0;
    const { limitValue, motherAvailableValue } = this.params.motherCard;
    return Math.min(motherAvailableValue, limitValue);
  }

  async getGasDecrease() {
    const { memberData, goodsItem } = this.parent.params;
    if (+this.originMoney === 0) {
      return {
        success: false,
      };
    }
    let motherMemberGuid = "";
    if (this.checkMother) {
      motherMemberGuid = this.params?.motherCard.motherCardGuid;
    }

    const result = await API.getGasDecrease({
      memberGuid: memberData.memberGuid,
      motherMemberGuid,
      goodsItemGuid: goodsItem.goodsItemGuid,
      price: goodsItem.isBargain ? goodsItem.bargainPrice : goodsItem.price,
      totalMoney: this.originMoney,
      paidDiscount: this.parent.discountCalc.getResult().money,
      paidFullCut: this.parent.activityCalc.getResult().money,
      paidPoint: this.parent.pointCalc.getResult().money,
      paidCoupon: this.parent.couponCalc.getResult().money,
    });
    return {
      success: result.success,
      data: result.data?.valueGasDecreaseItem ?? null,
    };
  }

  async calc(v) {
    let value = 0;
    let params = null;
    let decreaseSuccess = true;
    let decreaseData = null;
    if (this.checkValue) {
      value = this.value;
      params = this.params?.value;
    } else if (this.checkMother) {
      value = this.motherValue;
      params = this.params?.motherCard;
    }

    if (params && params.oilDecreaseValue) {
      const result = await this.getGasDecrease();
      // console.log("-----获取后端每升减优惠-----");
      // console.table(result.data);
      decreaseSuccess = result.success;
      if (result.success) {
        value = result.data.paidValue;

        decreaseData = result.data;
      } else {
        value = 0;
      }
    } else {
      value = Math.min(value, this.remainingMoney);
    }
    this.result = {
      money: value,
      decreaseSuccess,
      decreaseData,
      motherCard: {
        total: this.motherValue,
        checked: this.checkMother,
        availableValue: this.motherAvailableValue,
      },
      childCard: {
        total: this.value,
        checked: this.checkValue,
        availableValue: this.availableValue,
      },
    };
  }
}

export default class CalculatorContext {
  static dftCheckMap = {
    discount: false,
    activity: false,
    coupon: false,
    point: false,
    motherCard: false,
    value: false,
  };
  constructor(params) {
    this.params = params;
    this.buildCalculatorChain();
  }

  buildCalculatorChain() {
    this.discountCalc = new DiscountCalculator(this.params.discount);
    this.activityCalc = new MultiActivityCalculator(this.params.activity);
    this.couponCalc = new CouponCalculator(this.params.coupon);
    this.pointCalc = new PointCalculator(this.params.point);
    this.valueCalc = new ValueCalculator(this.params.value, this);

    this.map = [
      this.discountCalc,
      this.activityCalc,
      this.couponCalc,
      this.pointCalc,
      this.valueCalc,
    ];

    this.discountCalc.setNext(this.activityCalc);
    this.activityCalc.setNext(this.couponCalc);
    this.couponCalc.setNext(this.pointCalc);
    this.pointCalc.setNext(this.valueCalc);
  }

  getRemainingMoney(i) {
    var temp = this.price;
    while (i >= 0) {
      temp = Math.subtract(temp, this.map[i].result.money);
      i--;
    }
    return temp;
  }

  getPayCash() {
    let result = this.map.reduce(
      (pre, crt) => Math.subtract(pre, crt.result.money),
      this.price
    );
    const value = this.valueCalc.getResult();
    if (value.decreaseData) {
      result = value?.decreaseData?.lastPaid ?? 0;
    }
    return Math.max(result, 0);
  }

  // 计算单次优惠
  // 总金额，勾选对象，总升数
  async compute({ price, checkMap, totalUnit }) {
    this.price = price;
    this.map.forEach((it) => it.checkByMap(checkMap));
    // 储值优惠需要请求后端
    await this.discountCalc.handle(price, price, totalUnit);
    return {
      originMoney: price,
      discount: this.discountCalc.getResult(),
      activity: this.activityCalc.getResult(),
      point: this.pointCalc.getResult(),
      coupon: this.couponCalc.getResult(),
      value: this.valueCalc.getResult(),
      payCash: this.getPayCash(),
    };
  }

  // 在若干个互斥的优惠项目中选出最优的一个
  static async calc(params, options, { conflict, share }, change) {
    const calc = new CalculatorContext(params);
    const checkMap = { ...this.dftCheckMap, ...options.checkMap };
    const result = await calc.compute({ ...options, checkMap });

    // 手动勾选导致的计算,直接返回
    if (change.changeByCheck) {
      return { result };
    }

    if (!conflict.length) return { result };
    // 某个互斥支付方式可能已勾选
    const checkedConflict = conflict.find((k) => checkMap[k]);
    // if (checkedConflict) return { result };

    // 储值在同享优惠中
    const shareHas = (k) => share.includes(k);
    const valueInShare =
      (shareHas("value") && checkMap["value"]) ||
      (shareHas("motherCard") && checkMap["motherCard"]);

    const list = [...conflict];

    console.log(list);

    const results = [];
    const checkMaps = [];

    // 循环计算每种勾选状况的优惠结果
    while (list.length) {
      const key = list.shift();
      const map = { ...checkMap };
      if (checkedConflict) {
        map[checkedConflict] = false;
      }
      map[key] = true;
      // 储值在同享优惠中,因为可能需要异步请求,放到最后计算
      // 储值的计算优先级也是最后,暂时跳过对计算结果无影响
      // 这里先计算出未勾选储值时的优惠结果
      if (valueInShare) {
        if (shareHas("value")) {
          map.value = false;
        } else if (shareHas("motherCard")) {
          map.motherCard = false;
        }
      }
      checkMaps.push(map);
    }

    for (let i = 0; i < checkMaps.length; i++) {
      const map = checkMaps[i];
      // 勾选状况与传入的一致,跳过计算
      if (isSame(map, checkMap)) {
        results.push({ result, checkMap: map });
        continue;
      }
      const calc = new CalculatorContext(params);
      const res = await calc.compute({ ...options, checkMap: map });
      results.push({ result: res, checkMap: map });
    }

    const payCash = Math.min(...results.map(({ result }) => +result.payCash));
    const maxDiscount = results.find(
      ({ result }) => result.payCash === payCash
    );

    console.log("互斥优惠中这个选择最优", maxDiscount);

    // 储值不在同享中,无需补充上面跳过的计算
    if (!valueInShare) {
      if (isSame(checkMap, maxDiscount.checkMap))
        return { ...maxDiscount, checkMap: null };
      return maxDiscount;
    }
    if (shareHas("value")) {
      maxDiscount.checkMap.value = true;
    } else if (shareHas("motherCard")) {
      maxDiscount.checkMap.motherCard = true;
    }
    const res = await new CalculatorContext(params).compute({
      ...options,
      checkMap: maxDiscount.checkMap,
    });
    return { result: res, checkMap: maxDiscount.checkMap };
  }
}

function log(obj) {
  console.log(JSON.stringify(obj, null, 4));
}

function isSame(a, b) {
  if (a === b) return true;
  for (var key in a) {
    if (a[key] !== b[key]) return false;
  }
  return true;
}

function logDiscount(discount) {
  const labelMap = {
    discount: "满减",
    activity: "活动",
    point: "积分",
    coupon: "优惠券",
    value: "储值",
  };
  const map = Object.keys(discount).reduce(
    (map, name) => {
      if (!labelMap[name]) return map;
      return {
        ...map,
        [name]: {
          label: labelMap[name],
          value: discount[name].money,
        },
      };
    },
    {
      原价: discount.originMoney,
      还需支付: discount.payCash,
    }
  );
  console.log("优惠结果", map);
}

function fmtRes(res) {
  const obj = {};
  for (var key in res) {
    const value = res[key];
    if (typeof value === "object") {
      obj[key] = Object.keys(value).reduce((o, k) => {
        if (typeof value[k] === "object") return o;
        return { ...o, [k]: value[k] };
      }, {});
    } else {
      obj[key] = res[key];
    }
  }

  return obj;
}
