<template>
  <div class="staff-panel">
    <div class="cell" @click="pickStaff">
      <template v-if="pickedStaff.length === 0">
        <svg-icon name="staff" />
        <span>选择员工</span>
      </template>
      <p class="name" v-else>{{ displayStuff }}</p>
    </div>
  </div>
</template>

<script>
import pickStaff from "./index";
import { mapState } from "vuex";
export default {
  name: "StaffPanel",
  components: {},
  props: {},
  data() {
    return {};
  },
  computed: {
    ...mapState("Main", ["staffList", "pickedStaff", "noPayOrder"]),
    displayStuff() {
      return this.pickedStaff.map((it) => it.text).join(",");
    },
    // 当前所选员工是油机订单中的
    alreadyPickNoPayStaff() {
      if (!this.noPayOrder) return false;
      const keys = this.pickedStaff.map((it) => it.staffGuid);
      const { staffGuid } = this.noPayOrder;
      return keys.includes(staffGuid);
    },
  },
  methods: {
    async pickStaff() {
      if (this.alreadyPickNoPayStaff) {
        this.$message({
          type: "warning",
          message: "当前订单已有员工,不支持修改",
        });
        return;
      }

      if (!this.staffList.length) {
        return this.$message({
          type: "warning",
          message: "暂无员工",
        });
      }
      const result = await pickStaff({
        value: this.pickedStaff,
        options: this.staffList,
      });
      if (!result.success) return;
      this.$store.commit(
        "Main/SET_PICKED_STAFF",
        this.$utils.copy(result.value)
      );
    },
  },
  watch: {},
  created() {},
  mounted() {},
  beforeDestroy() {},
};
</script>

<style lang="less" rel="stylesheet/less">
.staff-panel {
  background-color: #403e3e;
  padding-top: 10px;
  .cell {
    opacity: 0.8;
    height: 48px;
    border: 1px solid #fff;
    border-radius: 4px;
    display: flex;
    align-items: center;
    padding: 14px;
    color: #fff;
    cursor: pointer;
    svg {
      font-size: 20px;
      margin-right: 10px;
    }
    .name {
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }
  }
}
</style>
