<template>
  <div class="gun-picker" :class="{ expend }">
    <StaffPanel/>
    <h3 class="gun-picker_title">{{oiltitle}}</h3>
    <div class="popover" ref="popover" :style="style"></div>
    <div class="expend-btn" @click="expend = !expend" v-if="isNeedLoopRequest">
      <i class="el-icon-arrow-right"></i>
    </div>
    <div class="gun-list" v-if="options.length" ref="list">
      <div
              class="gun-item"
              @click="pickItem(item, index)"
              :class="{
          picked: item[keyProps] === value,
          active: item.orders.length !== 0,
          normal: item.orders.length === 0,
        }"
              v-for="(item, index) in localOptions"
              :key="item[keyProps]"
      >
        <div class="gun-info">
          <span class="gun-index" v-if="item.gunName">{{
            item.gunName | num
          }}</span>
          <span class="gun-gas">{{ item.gsName }}</span>
        </div>

        <div class="roll-wrap" v-if="item.orders.length">
          <Roller :list="item.orders">
            <template v-slot="order">
              <div class="order-item">
                <span class="order_price">{{ order.money }}</span>
                <span class="order_time">{{ order.time }}</span>
              </div>
            </template>
          </Roller>
        </div>
        <!--        <div class="popover" ref="popover"></div>-->
      </div>
    </div>
    <div class="gun-empty" v-else>
      <p>您还没有设置油品!</p>
    </div>
  </div>
</template>

<script>
  const HEIGHT = document.documentElement.clientHeight;
  import pickOrder from "@/services/pickOrder";
  import { mapState, mapGetters } from "vuex";
  import Roller from "@/components/Roller";

  export default {
    name: "GunPicker",
    props: {
      options: Array,
      value: String,
      keyProps: {
        type: String,
        default: "id"
      }
    },
    filters: {
      num: (str) => str.replace(/^(\d*).*$/, "$1")
    },
    components: { Roller },
    computed: {
      ...mapState("Main", ["orderList", "staffList", "pickedStaff"]),
      ...mapGetters("Main", ["isNeedLoopRequest"]),
      localOptions() {
        return this.options.map((item) => ({
          ...item,
          orders: this.orderList.filter((it) => it.oilGunGuid === item.guid)
        }));
      },
      oiltitle() {
        let Guns = this.options.filter((item) =>
          item.guid != "00000000-0000-0000-0000-000000000000"
        );
        return Guns.length > 0 ? "选择油枪" : "选择油品";
      }
    },
    data() {
      return {
        currentPopover: "",
        rectList: [],
        style: {},
        expend: false
      };
    },
    created() {
    },
    methods: {
      async pickItem(item, index) {
        if (item.orders.length) {
          this.handlerPosition(index);
          console.log(item.orders);
          const order = await pickOrder({
            options: item.orders.map((it) => ({ ...it, key: it.oilGunGuid })),
            el: this.$refs["popover"]
          });
          console.log(order);
          if (!order.success) return;
          this.$store.commit("Main/SET_NO_PAY_ORDER", order.value);
          // 将订单所带员工设置为已选员工
          this.pickNoPayOrderStaff(order.value?.staffGuid ?? "");
          if (order.value) {
            this.$store.commit("Main/SET_ORDER_MONEY", order.value.money);
          }
        } else {
          this.$store.commit("Main/SET_NO_PAY_ORDER", null);
        }
        console.log(item[this.keyProps], "asdf");
        this.expend = false;
        this.$emit("input", item[this.keyProps]);
      },

      // 将油机订单中的员工设置为已选员工
      pickNoPayOrderStaff(staffGuid) {
        if (this.pickedStaff.length) {
          this.$message({
            type: "warning",
            message: "您选择了新订单,员工已更新"
          });
          this.$store.commit("Main/SET_PICKED_STAFF", []);
        }
        if (staffGuid) {
          const staff = this.staffList.find((it) => it.staffGuid === staffGuid);
          if (!staff) {
            this.$message({
              type: "error",
              message: "该笔订单的员工不存在,请检查"
            });
            return;
          }
          this.$store.commit("Main/SET_PICKED_STAFF", [staff]);
        }
      },

      handlerPosition(index) {
        const el = this.$refs["list"];
        if (!el) return;
        const item = [...el.children][index];
        if (!item) return;
        const rect = item.getBoundingClientRect();
        console.log(rect);
        this.style = {
          top: rect.top + "px",
          left: rect.width + 50 + "px"
        };

        if (this.expend) {
          this.style.left = rect.left + rect.width / 2 - 100 + "px";
          this.style.top = rect.top + rect.height + 10 + "px";
          if (rect.top + 260 > HEIGHT) {
            this.style.left = rect.left + "px";
          }
        }
        if (rect.top + 260 > HEIGHT) {
          this.style.top = rect.top - 260 + rect.height + "px";
        }
      }
    }
  };
</script>

<style lang="less" rel="stylesheet/less">
  .gun-picker {
    background-color: #403e3e;
    color: #878787;
    position: relative;
    font-size: @font-size-small;
    padding: 0 14px;
    height: 100%;

    transition: width 0.3s;

    &.expend {
      width: 62.5vw;

      .gun-list {
        display: flex;
        flex-wrap: wrap;
        height: auto;

        .gun-item {
          width: 17vw;
          margin: 1vw;

          &.normal {
            padding-top: 25px;
          }
        }
      }

      .expend-btn {
        i {
          transform: rotate(180deg);
        }
      }
    }

    .expend-btn {
      position: absolute;
      right: -16px;
      top: 50%;
      transform: translateY(-50%);
      width: 16px;
      height: 60px;
      background-color: #403e3e;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;

      i {
        color: #fff;
      }

      &:before {
        top: -8px;
      }

      &:after {
        bottom: -8px;
        transform: rotateX(180deg);
      }

      &:before,
      &:after {
        content: "";
        position: absolute;
        left: 0;
        width: 0;
        height: 0;
        border: 4px solid #403e3e;
        border-right-width: 8px;
        border-left-width: 8px;
        border-top-color: transparent;
        border-right-color: transparent;
      }
    }

    .popover {
      position: fixed;
      z-index: 1;
    }

    .gun-picker_title {
      font-weight: normal;
      line-height: 65px;
    }

    .gun-empty {
      height: 80%;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .gun-list {
      height: ~"calc(100% - 123px)";
      overflow: auto;

      &:hover {
      }
    }

    .gun-item {
      cursor: pointer;
      margin-bottom: 16px;
      padding: 10px 0;
      background-color: #5e5c5c;
      color: #fff;
      border-radius: @border-radius-middle;
      position: relative;

      &.active {
        background-color: #2d64b2;
        color: #fff;
      }

      &.normal {
        padding: 15px 0;

        .gun-index {
          color: #fe9700;
        }
      }

      &.picked {
        background-color: @active-color;
      }

      .gun-info {
        display: flex;
        align-items: center;

        .gun-index {
          text-align: center;
          width: 46px;
          flex-shrink: 0;
          flex-basis: 46px;
          display: inline-block;
          position: relative;
          font-size: @font-size-big;

          &:after {
            content: "";
            position: absolute;
            bottom: 0;
            right: 0;
            top: 0;
            transform: scale(0.5);
            transform-origin: 50% 50%;
            border-right: 1px solid #fff;
          }

          & + .gun-gas {
            font-size: @font-size-small;
          }
        }

        .gun-gas {
          padding: 0 15px;
          font-size: @font-size-default;
        }
      }

      .roll-wrap {
        height: 30px;
        overflow: hidden;
      }

      .order-item {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        padding: 0 20px;
        opacity: 0.6;

        .order_price {
          font-size: 16px;
          font-weight: bold;

          &:before {
            content: "￥";
            font-size: 12px;
          }
        }

        .order_time {
          font-size: 14px;
        }
      }
    }

    &:hover {
      ::-webkit-scrollbar-thumb {
        background-color: #aaa;
      }
    }
  }
</style>
