const dftCouponOptions = {
  isAllowMultiCoupon: true,
  couponConsumeMaxRate: 100,
  list: [],
};

export default class Coupon {
  static DJQ = 2;
  static ZKQ = 3;

  // region 类属性
  // 消费金额
  money = 0;

  // 已选代金券
  pickedDjq = [];

  // 已选代金券金额之和
  get usedDjqDiscount() {
    return this.pickedDjq.reduce(
      (pre, coupon) =>
        Math.add(pre, Math.mult(coupon.count, coupon.couponValue)),
      0
    );
  }

  // 已选代金券门槛之和
  get usedDjqThreshold() {
    return this.pickedDjq.reduce(
      (pre, coupon) =>
        Math.add(pre, Math.mult(coupon.count, coupon.minConsumeValue)),
      0
    );
  }

  // 已选折扣券
  pickedZkq = [];

  get usedZkqDiscount() {
    const item = this.pickedZkq[0];
    if (!item) return 0;
    return Math.mult(this.money, Math.subtract(1, item.couponValue));
  }

  // 最大可抵扣的金额
  get MAX_DISCOUNT_MONEY() {
    // 用券前的消费金额 * 系数
    return this.money * this.params.couponConsumeMaxRate;
  }

  // 所有可用券数量
  get totalCount() {
    const counter = (list) =>
      list.reduce((p, i) => (i.disabled ? p : p + i.number), 0);
    return counter(this.zkq) + counter(this.djq);
  }

  // endregion

  constructor(params) {
    params.list.forEach((it) => (it.key = it.couponSendGuid));
    this.params = { ...dftCouponOptions, ...params };
  }

  // 计算属性 代金券列表
  /*
  代金券列表为券中couponType为2的
  它的可用状态需要根据当前消费金额,消费门槛,动态计算
  如果一个代金券在被选中的代金券中
  它的checked为true,count为被选中的count
  它的最大可选数量由当前已选代金券与以上状态决定
  */
  get djq() {
    return this.params.list.reduce((pre, item) => {
      if (item.couponType !== Coupon.DJQ) return pre;
      const picked = this.pickedDjq.find((it) => it.key === item.key);
      const checked = !!picked;
      const count = picked ? picked.count : 0;
      const maxCount = this.getMaxCount({ ...item, count });
      const disabled = !this.isEnable({ ...item, count });
      return [...pre, { ...item, disabled, checked, count, maxCount }];
    }, []);
  }

  // 计算属性 折扣券列表
  get zkq() {
    return this.params.list.reduce((pre, item) => {
      if (item.couponType !== Coupon.ZKQ) return pre;
      const checked = this.isPicked(item);
      const count = checked ? 1 : 0;
      const disabled = !this.isEnable({ ...item, count });
      const maxCount = 1;
      return [...pre, { ...item, disabled, checked, count, maxCount }];
    }, []);
  }

  // region 券属性
  // 计算一张券是否可用
  isEnable(coupon) {
    switch (coupon.couponType) {
      case Coupon.DJQ:
        return this.isDjqEnable(coupon);
      case Coupon.ZKQ:
        return this.isZkqEnable(coupon);
    }
    return false;
  }

  isDjqEnable(coupon) {
    // 参数规定无法叠加
    if (!this.params.isAllowMultiCoupon) {
      if (this.pickedDjq.length) return this.isPicked(coupon);
    }

    return this.getMaxCount(coupon) > 0;
  }

  // 计算券的最大可用张数
  getMaxCount(coupon) {
    let usedThreshold = this.usedDjqThreshold;

    // 消费金额-已用券门槛之和再-已用券金额 为剩余可用门槛
    const restThreshold = this.money - usedThreshold;

    if (coupon.minConsumeValue === 0) {
      if (!this.params.isAllowMultiCoupon && coupon.number > 1) {
        return 1;
      }
      return coupon.number;
    }

    let enableCount = Math.floor(restThreshold / coupon.minConsumeValue);

    if (enableCount > coupon.number - coupon.count) {
      enableCount = coupon.number - coupon.count;
    }

    let maxCount = enableCount + coupon.count;

    // 如果不允许多选
    if (!this.params.isAllowMultiCoupon && maxCount > 1) {
      maxCount = 1;
    }

    return maxCount;
  }

  // 折扣券互斥
  isZkqEnable(coupon) {
    if (coupon.minConsumeValue > this.money) return false;
    return this.pickedZkq.length === 0 || this.pickedZkq[0].key === coupon.key;
  }

  // endregion

  // region 方法
  isPicked(item) {
    let list = [];
    switch (item.couponType) {
      case Coupon.ZKQ:
        list = this.pickedZkq;
        break;
      case Coupon.DJQ:
        list = this.pickedDjq;
        break;
    }
    return !!list.find((it) => it.key === item.key);
  }

  setMoney(money) {
    this.money = money;
  }

  // 取一张券
  take(item) {
    item = { ...item };
    switch (item.couponType) {
      case Coupon.DJQ:
        if (item.disabled) return;
        if (this.pickedDjq.find((it) => it.key === item.key)) return;
        item.count = 1;
        this.pickedDjq.push(item);
        break;
      case Coupon.ZKQ:
        item.count = 1;
        this.pickedZkq = [item];
        break;
    }
  }

  // 丢掉一张券
  throw(item) {
    switch (item.couponType) {
      case Coupon.DJQ:
        this.pickedDjq = this.pickedDjq.filter((it) => it.key !== item.key);
        break;
      case Coupon.ZKQ:
        this.pickedZkq = [];
        break;
    }
  }

  // 修改券的数量
  setCouponCount(item) {
    // 券的数量超过最大可用数量,弹出去
    if (item.count > item.maxCount) return;
    this.pickedDjq = this.pickedDjq.map((it) =>
      it.key === item.key ? { ...it, count: item.count } : it
    );
    this.pickedZkq = this.pickedZkq.map((it) =>
      it.key === item.key ? { ...it, count: item.count } : it
    );
  }

  result(couponType) {
    let coupons = [];
    let money = 0;

    switch (couponType) {
      case Coupon.ZKQ:
        coupons = this.pickedZkq;
        // 折扣券不受最大抵扣比例参数限制
        money = this.usedZkqDiscount; // Math.min(this.usedZkqDiscount, this.MAX_DISCOUNT_MONEY);
        break;
      case Coupon.DJQ:
        money = Math.min(this.usedDjqDiscount, this.MAX_DISCOUNT_MONEY);
        coupons = this.pickedDjq;
        break;
    }

    return {
      coupons: coupons.map((it) => ({
        couponSendNoteGuid: it.couponSendGuid,
        couponGuid: it.couponGuid,
        value: it.couponValue,
        name: it.title,
        number: it.count,
      })),

      money: Math.changeToDecimal(money, 2), // parseFloat(money.toFixed(2)),
      count: coupons.reduce((p, it) => p + it.count, 0),
      enabledCount: this.totalCount,
    };
  }

  // endregion

  // region 工具方法
  static getResult(params, money, pickedCoupons) {
    const coupon = new Coupon(params);
    coupon.setMoney(money);
    pickedCoupons.forEach((it) => {
      coupon.take(it);
      coupon.setCouponCount(it);
    });

    if (pickedCoupons.length) {
      const type = pickedCoupons[0].couponType;
      return coupon.result(type);
    }
    return {
      total: 0,
      count: 0,
      money: 0,
      enabledCount: coupon.totalCount,
    };
  }

  // 模拟用户操作,自动勾选,返回优惠最大的结果
  static autoTakeCoupon(params, money) {
    const coupon = new Coupon(params);
    coupon.setMoney(money);
    coupon.pickedDjq = [];

    // region 勾选代金券,计算优惠
    for (let i = 0; i < coupon.djq.length; i++) {
      const item = coupon.djq[i];
      const usedMoney = coupon.usedDjqDiscount;
      if (usedMoney > coupon.MAX_DISCOUNT_MONEY) break;
      const count = Math.min(
        Math.ceil((coupon.MAX_DISCOUNT_MONEY - usedMoney) / item.couponValue),
        item.maxCount
      );
      if (count === 0) continue;
      coupon.take(item);
      coupon.setCouponCount({ ...item, count });
    }
    let djqResult = coupon.result(Coupon.DJQ);
    // endregion

    // region 勾选折扣券,计算优惠
    // 找出折扣券中折扣最大的券
    const minValue = Math.min(
      ...coupon.zkq.filter((it) => !it.disabled).map((it) => it.couponValue)
    );
    const zkq = coupon.zkq.find((it) => it.couponValue === minValue);
    if (zkq) {
      coupon.pickedZkq = [zkq];
      coupon.setCouponCount({ ...zkq, count: 1 });
    }
    const zkqResult = coupon.result(Coupon.ZKQ);
    // endregion

    return +djqResult.money > +zkqResult.money ? djqResult : zkqResult;
  }

  // endregion
}
