<template>
  <div class="number-keyboard">
    <table cellpadding="0" cellspacing="0" border="0">
      <tbody>
        <tr v-for="item in list">
          <td
            @click="pressItem({ value: it.value, type: it.type })"
            :rowspan="it.value === '00' ? '2' : ''"
            :colspan="it.value === '0' ? '2' : ''"
            v-for="it in item"
          >
            <div
              class="keyboard-cell shadow-press"
              :class="it.type + (disabled ? ' disabled' : '')"
            >
              <template v-if="it.type === 'text'">清空</template>
              <template v-if="it.type === 'num'">{{ it.value }}</template>
              <template v-if="it.type === 'money'">
                <span
                  ><small style="font-size: 18px;">￥</small
                  >{{ it.value }}</span
                >
              </template>
              <template v-if="it.type === 'icon'">
                <svg-icon name="backspace" />
              </template>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "Keyboard",
  props: {
    value: String,
    disabled: Boolean,
    free: Boolean, // 是否限制输入
    max: {
      type: Number,
      default: 99999.99
    },
    precision: {
      // 小数精度
      type: Number,
      default: 2
    },
    focusedElem: null // 键盘输入对象
  },
  data() {
    return {
      icon: {
        c: "icon-zizhumaidanshouqijianpan",
        x: "icon-zizhumaidanshanchu"
      },
      list: [
        [
          { type: "num", value: "1" },
          { type: "num", value: "2" },
          { type: "num", value: "3" },
          { type: "icon", value: "x" },
          { type: "money", value: "100" }
        ],
        [
          { type: "num", value: "4" },
          { type: "num", value: "5" },
          { type: "num", value: "6" },
          { type: "text", value: "c" },
          { type: "money", value: "200" }
        ],
        [
          { type: "num", value: "7" },
          { type: "num", value: "8" },
          { type: "num", value: "9" },
          { type: "num", value: "00" },
          { type: "money", value: "300" }
        ],
        [
          { type: "num", value: "0" },
          { type: "num", value: "." },
          { type: "money", value: "500" }
        ]
      ]
    };
  },
  methods: {
    pressItem({ type, value }) {
      // 获取input的光标位置
      // 来自正在接受键盘输入值的组件对象中
      // 默认为文本末尾
      const focusIndex =
        this.focusedElem?.getFocusIndex() ?? this.vValue.length;
      // 当前键盘接受组件的input没有处于focus状态,将其focus
      if (this.focusedElem) {
        if (this.focusedElem.$refs?.input !== document.activeElement) {
          this.focusedElem.$refs?.input?.focus();
        }
      }
      if (this.disabled) return;
      if (type === "money") {
        this.vValue = value.split("");
      } else {
        let list = [...this.vValue];
        // 用光标位置截断文本,在中间插入新的字符
        let start = list.slice(0, focusIndex);
        let end = list.slice(focusIndex, list.length);
        if (this.free) {
          if (value === "x")
            return (this.vValue = [
              ...start.slice(0, start.length - 1),
              ...end
            ]);
          if (value === "c") return (this.vValue = ["0"]);
          return (this.vValue = [...start, value, ...end]);
        }
        // 以下没有处理光标位置
        switch (value) {
          case "x":
            list.pop();
            break;
          case "c":
            list = ["0"];
            break;
          case ".":
            if (list.length === 0) {
              list = ["0", "."];
            } else if (list.indexOf(".") < 0) {
              list.push(".");
            }
            break;
          case "00":
            if (list[0] !== "0" && list[0]) {
              list.push("0");
              list.push("0");
            }
            if (!list[0]) {
              list.push("0");
            }
            break;
          default:
            list[0] === "0" && !list.includes(".") && list.pop();
            list.push(value);
            break;
        }

        if (list.indexOf(".") > 0) {
          list = list.slice(0, list.indexOf(".") + (this.precision + 1));
        }

        if (+list.join("") > this.max) {
          list = [...this.vValue];
        }

        this.vValue = list;
      }
    }
  },
  computed: {
    vValue: {
      get() {
        return (this.value + "").split("");
      },
      set(v) {
        this.$emit("input", v.join(""));
      }
    }
  }
};
</script>

<style lang="less" rel="stylesheet/less">
.number-keyboard {
  height: 100%;
  margin: -5px -5px 0;

  table {
    height: 100%;
    width: 100%;

    tr {
      height: 25%;

      td {
        text-align: center;
        width: 20%;
        box-sizing: border-box;
        position: relative;

        .keyboard-cell {
          user-select: none;
          cursor: pointer;
          color: #000;
          background-color: #fff;
          border-radius: @border-radius-small;
          display: flex;
          align-items: center;
          justify-content: center;

          position: absolute;
          bottom: 5px;
          right: 5px;
          top: 5px;
          left: 5px;

          &:active {
            overflow: hidden;
            background-color: #e7e7e7;
          }

          &.num {
            font-size: @font-size-great;
          }

          &.money {
            font-size: @font-size-big;
            background-color: #1bb987;
            color: #fff;
          }

          &.text {
            font-size: @font-size-normal;
          }

          &.disabled {
            opacity: 0.6;
            touch-action: none;
            pointer-events: none;
            cursor: not-allowed;
          }
        }

        svg {
          font-size: @font-size-great;
          color: #000;
        }
      }
    }
  }
}
</style>
