/*      ///支付方式,采取的是位运算(支付宝1),(微信2),(储值4),(积分8),(现金16),(银行卡32),(优惠券64) ,
        ///(其它128),(云闪付256),(翼支付512),(分期支付1024),(电子社保码2048),(电子健康码4096),(闪付8192)
        ///0表示没开启支付方式,如3代表开启了支付宝和微信支付
*/

/*
         ///优惠方式,采取的是位运算优惠互斥方式(满减/随机减1)、(优惠券2)、(会员折扣4)、(积分8)、(储值16),
*/

const options = [
  { value: 1, name: "alipay", label: "支付宝" },
  { value: 2, name: "wechat", label: "微信" },
  { value: 4, name: "value", label: "储值" },
  { value: 8, name: "point", label: "积分" },
  { value: 16, name: "cash", label: "现金" },
  { value: 32, name: "bank", label: "银行卡" },
  { value: 64, name: "coupon", label: "优惠券" },
  { value: 128, name: "other", label: "其他" },
  { value: 256, name: "union", label: "云闪付" },
  { value: 512, name: "best", label: "翼支付" },
];

const conflictOptions = [
  { value: 1, name: "activity", label: "活动" },
  { value: 2, name: "coupon", label: "优惠券" },
  { value: 4, name: "discount", label: "会员折扣" },
  { value: 8, name: "point", label: "积分" },
  { value: 16, name: "value", label: "储值" },
];

// 支付方式中包含优惠方式
const discountMap = [4, 8, 64];

export function getUsedPaytype(params, activity, discount, motherInfo) {
  const { conflict, payType } = params;
  // 开启了的支付方式
  const usedList = options.filter((it) => (it.value & payType) > 0);
  // console.log(usedList, payType);
  //  开启了的优惠支付方式,可能包括活动,折扣
  const discountList = usedList
    .filter((it) => discountMap.includes(it.value))
    .concat([activity, discount].filter(Boolean));
  // 互斥项目的name
  const conflictList = conflictOptions
    .filter((it) => (it.value & conflict) > 0)
    .map((it) => it.name);

  function insertMotherCard(list) {
    const index = list.findIndex((it) => it.name === "value");
    console.log(index, list.map((i) => i.name).join(","));
    if (index === -1) return;
    list.splice(index, 0, motherInfo);
  }

  const shareDiscount = discountList.filter(
    (it) => !conflictList.includes(it.name)
  );
  const conflictDiscount = discountList.filter((it) =>
    conflictList.includes(it.name)
  );

  // 只要有母卡,就将母卡插入储值前面
  if (motherInfo) {
    insertMotherCard(shareDiscount);
    insertMotherCard(conflictDiscount);
  }

  // console.log(
  //   `支付方式:\n--------同享优惠方式: ${shareDiscount.map(
  //     (i) => i.name
  //   )}\n--------互斥优惠方式: ${conflictDiscount.map((i) => i.name)}`
  // );

  return {
    // 可同享优惠方式
    shareDiscount,
    // 互斥优惠方式
    conflictDiscount,
    // 第三方支付即除优惠方式外的支付方式
    thirdPaytype: usedList.filter((it) => !discountMap.includes(it.value)),
  };
}

// 计算第三方支付方式options
export function getThirdPaytype(list, params) {
  // console.log("getThirdPaytype--list---", list);
  const result = [];
  const cash = {
    name: "cash",
    label: "现金",
    value: 1,
  };
  const bank = {
    name: "bank",
    label: "银行卡",
    value: 3,
  };
  const mobile = {
    name: "mobile",
    label: "移动支付",
    value: 4,
    includes: [],
  };
  const other = {
    name: "other",
    label: "其他",
    value: 5,
    subOptions: (params?.payParameter.otherNameOnConsume ?? "")
      .split(",")
      .map((n) => ({ name: n, label: n, value: n })),
  };

  if (list.findIndex((it) => it.name === "cash") > -1) {
    result.push(cash);
  }
  if (list.findIndex((it) => it.name === "bank") > -1) {
    result.push(bank);
  }
  // 第三方支付通道(1,支付宝;2,微信;256,云闪付;512,翼支付)
  const mobileOptions = list.filter((it) =>
    ["alipay", "wechat", "union", "best"].includes(it.name)
  );
  if (mobileOptions.length > 0) {
    mobileOptions.forEach((it) => {
      switch (it.name) {
        case "alipay":
          it.value = 1;
          it.key = "AliPay";
          break;
        case "wechat":
          it.value = 2;
          it.key = "WeChatPay";
          break;
        case "union":
          it.value = 256;
          it.key = "UnionPay";
          break;
        case "best":
          it.value = 512;
          it.key = "BestPay";
          break;
      }
    });
    mobile.includes = mobileOptions;

    result.push(mobile);
  }
  if (list.findIndex((it) => it.name === "other") > -1) {
    result.push(other);
  }
  // console.log(result);
  return result;
}
