<template>
  <el-dialog
    :visible.sync="show"
    title="选择卡号"
    class="member-picker"
    width="640px"
    @closed="close"
  >
    <div class="member-picker-body">
      <section class="member-list">
        <el-table
          :data="options"
          border
          height="215"
          style="border-radius: 6px"
        >
          <el-table-column prop="cardId" label="卡号" />
          <el-table-column prop="trueName" label="姓名" />
          <el-table-column prop="mobile" label="手机号" />
          <el-table-column prop="memberGroupName" label="会员级别" />
          <el-table-column>
            <template v-slot="scope">
              <button
                class="shadow-press pick-btn"
                style="pointer-events: all"
                :title="scope.row.title"
                :disabled="scope.row.disabled"
                @click="pickItem(scope.row)"
              >
                选取
              </button>
            </template>
          </el-table-column>
        </el-table>
      </section>

      <footer>
        <button class="confirm-btn shadow-press" @click="onCancel">取消</button>
      </footer>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: "MemberPicker",
  components: {},
  data() {
    return {
      show: false,
      options: []
    };
  },
  created() {},
  methods: {
    close() {
      this.$destroy();
    },
    pickItem(item) {
      this.onConfirm(item);
    },
    onConfirm(data) {
      this.resolve({ success: true, data });
      this.show = false;
    },
    onCancel() {
      this.resolve({ success: false, data: null });
      this.show = false;
    }
  },
  computed: {},

  destroyed() {
    this.$el && this.$el.remove();
  }
};
</script>

<style lang="less" rel="stylesheet/less">
.member-picker {
  .member-list {
    color: @text-color-primary;

    th {
      border: 0;
      background-color: #eeeeee;
    }

    .pick-btn {
      width: 75px;
      height: 38px;
      border: 0;
      background-color: @blue-color;
      border-radius: @border-radius-middle;
      color: #fff;
    }
  }

  footer {
    padding-top: 30px;
    text-align: center;

    .confirm-btn {
      background-color: @blue-color;
      color: @text-color-white;
      width: 250px;
      height: 56px;
      border-radius: @border-radius-large;
      border: 0;
    }
  }
}

/*region modify*/
.member-picker {
  .el-dialog {
    overflow: hidden;
    border-radius: @border-radius-middle;
  }

  .el-dialog__header {
    background-color: #3a7ddb;

    .el-dialog__title,
    .el-dialog__headerbtn .el-dialog__close {
      color: @text-color-white;
    }
  }

  .el-dialog__body {
  }
}

/*endregion*/
</style>
