<template>
  <div class="radio-panel">
    <div
      class="conflict-discount flex-panel"
      @click="pickItem(item)"
      :class="{ disabled, picked: value === item.name }"
      v-for="item in options"
      :key="item.name"
    >
      <p>
        <svg-icon :name="value === item.name ? 'radio' : 'radio_o'" />
        <span class="label">{{ item.label }}</span>
        <span class="sub-label" v-if="item.subLabel" :title="item.subLabel"
          >({{ item.subLabel | ellipsis(ellipsisCount) }})</span
        >
        <el-button
          class="add-value-btn"
          v-if="item.name === 'value'"
          size="mini"
          type="danger"
          @click.stop="$emit('value')"
          >充值</el-button
        >
        <DescriptionPop
          class="description-icon"
          v-if="item.description"
          :message="item.description"
        />
      </p>
      <p>
        <span class="sub-value" v-if="item.subValue"
          >({{ item.subValue }})</span
        >
        <template v-if="item.name === 'coupon' && 'coupon' === value">
          <span class="caller" @click.stop="$emit('coupon')"
            ><svg-icon name="edit"
          /></span>
        </template>
        <span class="value text-yellow" v-if="item.value">
          <!--每升减优惠明细-->
          <DescriptionPop
            width="300"
            class="description-icon"
            v-if="
              gasDecrease.length && ['value', 'motherCard'].includes(item.name)
            "
          >
            <span
              style="line-height: 2;"
              v-html="gasDecrease.join('<br>')"
            ></span>
          </DescriptionPop>
          <span v-if="item.value">-￥{{ item.value | fixed }}</span>
        </span>
      </p>
    </div>
  </div>
</template>

<script>
import DescriptionPop from "./DescriptionPop";
import CouponPop from "./CouponPop";
import { mapGetters, mapState } from "vuex";

export default {
  name: "RadioPanel",
  props: {
    options: Array,
    value: String,
    disabled: Boolean,
  },
  components: { DescriptionPop, CouponPop },
  data() {
    return {};
  },
  created() {},
  methods: {
    pickItem(item) {
      if (this.value === item.name) return this.$emit("input", "");
      this.$emit("input", item.name);
    },
  },
  computed: {
    ...mapGetters("Main", ["gasDecrease", "gasValue"]),
    ...mapState("App", ["width"]),
    ellipsisCount() {
      return this.width > 1024 ? 8 : 5;
    },
  },
};
</script>

<style lang="less" rel="stylesheet/less">
.radio-panel {
}

.value-gas {
  color: #6596e1;
}
</style>
