<template>
  <div class="aspect-ratio" :style="style">
    <slot></slot>
  </div>
</template>

<script>
  export default {
	name: 'AspectRatio',
	props: {
	  ratio: {
		type: Number,
		required: true,
	  }
	},
	components: {},
	data() {
	  return {
		style: {}
	  };
	},
	created() {
	},
	methods: {
	  computeSize() {
		console.log('compute');
		const el = this.$el;
		if (!el) return;
		const {clientWidth, clientHeight} = el.parentNode;
		const RATIO = clientWidth / clientHeight;
		let width = clientWidth, height = clientHeight;
		if (RATIO > this.ratio) {
		  height = clientHeight;
		  width = clientHeight * this.ratio;
		}
		else {
		  // width = clientWidth;
		  // height = clientWidth / this.ratio;
		}
		this.style = {
		  width: width + 'px',
		  height: height + 'px',
		};
	  }
	},
	computed: {
	  WIDTH() {
		return this.$store.state.App.width;
	  },
	},
	watch: {
	  WIDTH() {
		console.log('change');
		this.computeSize();
	  }
	}
  };
</script>

<style lang="less" rel="stylesheet/less">

  .aspect-ratio {
  }
</style>
