<template>
  <el-dialog
    :visible.sync="show"
    title="选择优惠券"
    class="coupon-picker-action"
    width="822px"
    @closed="close"
  >
    <div class="coupon-picker">
      <header>
        <div class="menus">
          <div
            class="menu"
            @click="pickMenu(types.DJQ)"
            :class="pickedMenu === types.DJQ ? 'picked' : ''"
          >
            代金券
          </div>
          <div
            class="menu"
            @click="pickMenu(types.ZKQ)"
            :class="pickedMenu === types.ZKQ ? 'picked' : ''"
          >
            折扣券
          </div>
        </div>
        <div class="info" v-if="result">
          <span>已选</span>
          <i>{{ result.count }}</i>
          <span>张,抵扣</span>
          <i>{{ result.money }}</i>
          <span>元</span>
        </div>
      </header>

      <section :class="{ overflow: options.length > 4 }">
        <ul class="list">
          <li
            class="item"
            v-for="item in options"
            :key="item.key"
            :class="{
              checked: item.checked,
              disabled: item.disabled,
              coupon_zkq: item.couponType === types.ZKQ,
              coupon_djq: item.couponType === types.DJQ,
            }"
            @click="pickItem(item)"
          >
            <div class="left">
              <template v-if="item.couponType === types.ZKQ">
                <span>{{ item.couponValue | discount }}</span>
                <i>折</i>
              </template>
              <template v-if="item.couponType === types.DJQ">
                <i>￥</i>
                <span>{{ item.couponValue.toFixed(2).split(".")[0] }}</span>
                <i>.{{ item.couponValue.toFixed(2).split(".")[1] }}</i>
              </template>
            </div>

            <div class="right">
              <h3>{{ item.title }}</h3>
              <p>
                <span>{{ item.endDate }}到期</span>
                <span v-if="item.couponType === types.ZKQ"
                  >&times;{{ item.count }}</span
                >
              </p>
              <div v-if="item.couponType === types.DJQ">
                <el-input-number
                  @click.native.stop
                  :disabled="!item.checked"
                  size="mini"
                  :value="item.count"
                  @change="(v) => setCount({ ...item, count: v || 0 })"
                  :step="1"
                  :min="0"
                  :max="item.maxCount"
                ></el-input-number>
              </div>
            </div>

            <div class="check-box">
              <svg-icon
                :name="
                  (pickedMenu === types.ZKQ ? 'radio' : 'checkbox') +
                  (item.checked ? '' : '_o')
                "
              />
            </div>
          </li>
        </ul>
      </section>

      <footer>
        <el-button type="primary" @click="confirm">确定</el-button>
      </footer>
    </div>
  </el-dialog>
</template>

<script>
import Coupon from "@/utils/calculator/coupon.js";
const copy = (o) => JSON.parse(JSON.stringify(o));
export default {
  name: "coupon-picker",
  data() {
    return {
      show: false,
      params: {},
      value: [],
      money: 0,
      types: {
        ZKQ: Coupon.ZKQ,
        DJQ: Coupon.DJQ,
      },

      coupon: null,
      result: null,
      pickedMenu: Coupon.DJQ,
    };
  },
  filters: {
    discount: (v) => {
      return (v * 10)
        .toFixed(2)
        .replace(".00", "")
        .replace(/(\.[1-9])0$/, "$1");
    },
  },
  computed: {
    options() {
      switch (this.pickedMenu) {
        case Coupon.ZKQ:
          return this.zkq;
        case Coupon.DJQ:
          return this.djq;
      }
    },

    zkq() {
      if (!this.coupon) return [];
      return this.coupon.zkq;
    },
    djq() {
      if (!this.coupon) return [];
      return this.coupon.djq;
    },
  },
  methods: {
    pickMenu(type) {
      this.pickedMenu = type;
      this.result = this.coupon.result(type);
    },
    pickItem(item) {
      if (this.coupon.isPicked(item)) {
        this.coupon.throw(item);
      } else {
        this.coupon.take(item);
        // this.coupon.setCouponCount({ ...item, count: 1 });
      }
      this.result = this.coupon.result(this.pickedMenu);
    },

    setCount(item) {
      if (+item.count === 0) {
        this.coupon.throw(item);
      } else {
        this.coupon.setCouponCount(item);
      }
      this.result = this.coupon.result(this.pickedMenu);
    },
    close() {
      this.$destroy();
    },
    confirm() {
      let list = [];
      switch (this.pickedMenu) {
        case Coupon.DJQ:
          list = this.coupon.pickedDjq;
          break;
        case Coupon.ZKQ:
          list = this.coupon.pickedZkq;
          break;
      }
      this.resolve({
        success: true,
        data: this.result,
        list,
      });
      this.show = false;
    },
    onCancel() {
      this.resolve({
        success: false,
        data: null,
      });
      this.show = false;
    },
  },
  watch: {},
  created() {
    const [first] = this.value;
    this.coupon = new Coupon(this.params);
    this.coupon.setMoney(this.money);
    if (first) {
      this.pickedMenu = first.couponType;
      switch (first.couponType) {
        case Coupon.DJQ:
          this.coupon.pickedDjq = this.value;
          break;
        case Coupon.ZKQ:
          this.coupon.pickedZkq = this.value;
          break;
      }
    }
    this.result = this.coupon.result(this.pickedMenu);

    if (process.env.NODE_ENV === "development") {
      window.coupon = this.coupon;
      window.djq = copy(this.coupon.djq);
    }
  },
  mounted() {},
  beforeDestroy() {
    this.$el && this.$el.remove();
  },
};
</script>

<style lang="less" rel="stylesheet/less">
@active-color: #3a7ddb;
.coupon-picker-action {
  .el-dialog__header {
    height: 64px;
    background-color: @active-color;
    .el-dialog__title {
      color: #fff;
    }
    .el-icon-close {
      color: #fff;
    }
  }
  .el-dialog__body {
    padding: 0 20px;
  }
  .el-dialog {
    border-radius: 12px;
    overflow: hidden;
  }

  .coupon-picker {
    > header {
      display: flex;
      align-items: center;
      height: 70px;
      justify-content: space-between;
      .menus {
        display: flex;
        .menu {
          line-height: 40px;
          padding: 0 30px;
          border-radius: 21px;
          border: 1px solid transparent;
          margin-right: 15px;
          &.picked {
            font-weight: bold;
            border-color: @active-color;
            color: @active-color;
            background-color: #ebf3ff;
          }
        }
      }

      .info {
        i {
          color: #fe9700;
          margin: 0 5px;
        }
      }
    }

    section {
      height: 320px;
      overflow-y: auto;
      overflow-x: hidden;
      &.overflow {
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.05) inset;
      }
    }

    .list {
      display: flex;
      flex-wrap: wrap;
      padding: 0;
      margin: 0 -12px;

      .el-input-number--mini {
        width: 90px;
      }

      .item {
        list-style: none;
        height: 120px;
        width: 375px;
        border: 1px solid #e5e5e5;
        border-radius: 8px;
        margin: 12px;
        position: relative;

        display: flex;
        align-items: center;

        cursor: pointer;

        &.disabled {
          cursor: not-allowed;
        }

        &.coupon_djq {
          .left {
            font-size: 18px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }

        .left {
          width: 95px;
          display: flex;
          align-items: baseline;
          justify-content: center;
          color: #fd5451;
          font-size: 32px;
          font-weight: bold;

          i {
            font-size: 14px;
          }
        }

        .right {
          height: 80%;
          border-left: 1px dashed #e5e5e5;
          flex: 1;
          padding: 5px 50px 5px 10px;
          position: relative;

          display: flex;
          flex-direction: column;
          justify-content: space-between;

          h3 {
            font-size: 18px;
            line-height: 24px;
            font-weight: bold;
            .multi-line(2);
          }

          @size: 14px;
          &:before,
          &:after {
            background-color: #fff;
            box-sizing: border-box;
            content: "";
            position: absolute;
            width: @size;
            height: @size;
            left: @size / 2 * -1;
            border: 1px solid #e5e5e5;
            border-radius: 50%;
            border-top-color: transparent;
            border-left-color: transparent;
          }
          &:before {
            top: -5px - @size;
            transform: rotate(45deg);
          }
          &:after {
            bottom: -5px - @size;
            transform: rotate(-135deg);
          }
        }

        .check-box {
          font-size: 0;
          position: absolute;
          right: 15px;
          top: 50%;
          transform: translateY(-50%);
          svg {
            font-size: 24px;
          }
        }
      }
    }

    footer {
      text-align: center;
      padding: 20px;
      button {
        width: 200px;
        background-color: @active-color;
        border-color: @active-color;
      }
    }
  }
}
</style>
