<template>
  <div class="memo-panel shadow-normal">
    <input
      type="text"
      v-model="vValue"
      placeholder="备注"
      :disabled="disabled"
      tabindex="4"
    />
  </div>
</template>

<script>
export default {
  name: "Memo",
  props: {
    value: String,
    disabled: Boolean
  },
  components: {},
  data() {
    return {};
  },
  created() {},
  methods: {},
  computed: {
    vValue: {
      get() {
        return this.value;
      },
      set(v) {
        this.$emit("input", v);
      }
    }
  }
};
</script>

<style lang="less" rel="stylesheet/less">
.memo-panel {
  background-color: #fff;
  border-radius: @border-radius-middle;
  overflow: hidden;

  input {
    height: 42px;
    width: 100%;
    padding: 0 14px;
    border: 1px solid #fff;
    border-radius: @border-radius-middle;

    &:focus {
      border-color: @active-color;
    }
  }
}
</style>
