<template>
  <div class="share-panel">
    <div
      @click="pickItem(item)"
      class="share-discount flex-panel"
      :class="value.includes(item.name) && 'picked'"
      v-for="item in options"
      :key="item.name"
    >
      <p>
        <template v-if="!['discount', 'activity'].includes(item.name)">
          <svg-icon name="checkbox" v-if="value.includes(item.name)" />
          <svg-icon name="checkbox_o" v-else />
        </template>
        <span class="label">{{ item.label }}</span>
        <span class="sub-label" v-if="item.subLabel" :title="item.subLabel"
          >({{ item.subLabel | ellipsis(ellipsisCount) }})</span
        >
        <el-button
          size="mini"
          type="danger"
          class="add-value-btn"
          @click.stop="$emit('value')"
          v-if="item.name === 'value'"
          >充值</el-button
        >
        <DescriptionPop
          class="description-icon"
          v-if="item.description"
          :message="item.description"
        />
      </p>
      <p>
        <span class="sub-value" v-if="item.subValue"
          >({{ item.subValue }})</span
        >
        <template v-if="item.name === 'coupon' && item.coupons.length > 0">
          <span class="caller" @click.stop="$emit('coupon')"
            ><svg-icon name="edit"
          /></span>
        </template>
        <span
          class="value text-yellow"
          v-if="['activity', 'discount'].includes(item.name)"
          >-￥{{ item.value | fixed }}</span
        >
        <span class="value" v-else>
          <!--每升减优惠明细-->
          <DescriptionPop
            width="300"
            v-if="
              gasDecrease.length && ['value', 'motherCard'].includes(item.name)
            "
            class="description-icon"
          >
            <span
              style="line-height: 2;"
              v-html="gasDecrease.join('<br>')"
            ></span>
          </DescriptionPop>
          <span v-if="item.value">-￥{{ item.value | fixed }}</span>
        </span>
      </p>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";
import DescriptionPop from "./DescriptionPop";
import CouponPop from "components/Discount/CouponPop";

export default {
  name: "share-panel",
  props: {
    options: Array,
    value: Array,
    disabled: Boolean,
  },
  components: { DescriptionPop, CouponPop },
  data() {
    return {};
  },
  computed: {
    ...mapGetters("Main", ["gasDecrease"]),
    ...mapState("App", ["width"]),
    ellipsisCount() {
      return this.width > 1024 ? 8 : 5;
    },
  },
  methods: {
    pickItem(item) {
      // 禁用勾选同享优惠
      return;
      if (this.value.includes(item.name)) {
        this.$emit(
          "input",
          this.value.filter((key) => key !== item.name)
        );
      } else {
        this.$emit("input", [...this.value, item.name]);
      }
    },
  },
  watch: {},
  created() {},
  mounted() {},
  beforeDestroy() {},
};
</script>

<style lang="less" rel="stylesheet/less">
.share-panel {
  svg {
    margin-right: 5px;
  }
  .picked {
    svg {
      color: @active-color;
    }
  }
}
</style>
