<template>
  <div class="discount shadow-normal">
    <div class="discount-empty flex-center" v-if="isEmpty">
      <img :src="emptyImage" alt="" />
      <p>暂无优惠信息</p>
    </div>

    <div class="discount-panel">
      <SharePanel
        @coupon="callCouponPicker"
        @value="noticeAddValue"
        :options="shareOptions"
        v-model="currentShare"
      />
      <!--字母卡互斥-->
      <RadioPanel
        :options="valueOptions"
        @value="noticeAddValue"
        v-model="currentValue"
      />
      <h3 class="conflict-title" v-if="conflictOptions.length > 1">
        以下支付只可选一种
      </h3>

      <RadioPanel
        @coupon="callCouponPicker"
        @value="noticeAddValue"
        :options="conflictOptions"
        v-model="currentConflict"
      />
    </div>
  </div>
</template>

<script>
import * as EXT from "@/utils/ext";
import { mapState, mapGetters } from "vuex";
import emptyImage from "assets/discount_empty.png";
import RadioPanel from "./RadioPanel";
import SharePanel from "./SharePanel";
import callPicker from "./callCouponPicker";

const contain = (list) => (key) => list.includes(key);

export default {
  name: "Discount",
  props: {
    value: Object,
    disabled: Boolean,
  },
  components: { RadioPanel, SharePanel },
  data() {
    return {
      emptyImage,
      pickedValue: "",
    };
  },
  async created() {},
  methods: {
    async callCouponPicker() {
      if (!this.value.coupon) return;
      const coupons = (this.discountMap?.coupon.coupons ?? []).map((it) => {
        const item = this.coupon.list.find(
          (item) => item.couponSendGuid === it.couponSendNoteGuid
        );
        return { ...item, count: it.number };
      });

      const result = await callPicker({
        money: this.couponMoney,
        params: this.coupon,
        value: coupons,
      });

      if (!result.success) return;
      console.log(this.$utils.copy(result.list));
      this.$store.commit("Main/SET_PICKED_COUPONS", result.list);
      // 没有选择任何券,直接取消优惠券的勾选
      if (!result.list.length) {
        // 取消勾选也将触发更新
        this.$emit("input", { ...this.value, coupon: false });
      } else {
        // 触发更新
        const change = { changeByCheck: true };
        this.$store.dispatch("Main/computeDiscount", {
          money: this.orderMoney,
          change,
        });
      }
    },

    async noticeAddValue() {
      const result = await EXT.addValue({
        goodsItemGuid: this.goodsItemGuid,
        memberGuid: this.memberData.memberGuid,
      });

      if (!result.success)
        return this.$message({
          type: "error",
          message: result.message || "充值失败,请稍后再试",
        });
      this.$store.dispatch("Main/getMemberInfo");
    },
  },
  computed: {
    ...mapGetters("Main", ["gasDecrease", "gasValue", "coupon"]),
    ...mapState("Main", [
      "orderMoney",
      "discountMap",
      "memberData",
      "goodsItemGuid",
    ]),

    // 扣除会员优惠,活动,积分抵扣的消费金额后剩余的金额
    couponMoney() {
      let money = +this.orderMoney;
      const options = [...this.conflictOptions, ...this.shareOptions];
      if (this.value.discount) {
        money -= options.find((it) => it.name === "discount")?.value ?? 0;
      }
      if (this.value.activity) {
        money -= options.find((it) => it.name === "activity")?.value ?? 0;
      }

      return money;
    },

    // 原始互斥分组
    conflictList() {
      return this.$store.getters["Main/conflictDiscount"];
    },
    // 原始共享分组
    shareList() {
      return this.$store.getters["Main/shareDiscount"];
    },
    // region 虚拟绑定值
    // 勾选的同享项
    currentShare: {
      get() {
        const keys = this.shareOptions.map((i) => i.name);
        return keys.filter((key) => this.value[key]);
      },
      set(v) {
        const keys = this.shareOptions.map((i) => i.name);
        const value = { ...this.value };
        keys.forEach((key) => (value[key] = v.includes(key)));
        this.$emit("input", value);
      },
    },

    // 互斥项目
    currentConflict: {
      get() {
        // 互斥项的key
        const keys = this.conflictList.map((i) => i.name);
        return keys.find((key) => this.value[key]) || "";
      },
      set(v) {
        const keys = this.conflictList.map((i) => i.name);
        const value = { ...this.value };
        keys.forEach((key) => (value[key] = v === key));
        this.$emit("input", value);
      },
    },

    // 勾选的互斥项
    currentValue: {
      get() {
        const keys = this.valueOptions.map((i) => i.name);
        return keys.find((key) => this.value[key]) || "";
      },
      set(v) {
        const keys = this.valueOptions.map((i) => i.name);
        const value = { ...this.value };
        keys.forEach((key) => (value[key] = v === key));
        this.$emit("input", value);
      },
    },
    // endregion

    conflictOptions() {
      return this.conflictList;
    },
    shareOptions() {
      const has = contain(this.shareList.map((it) => it.name));
      // 原始同享数组中同时包含母卡和子卡时，将其剔除
      if (has("motherCard") && has("value")) {
        return this.shareList.reduce(
          (pre, it) =>
            ["motherCard", "value"].includes(it.name) ? pre : [...pre, it],
          []
        );
      }
      return this.shareList;
    },
    valueOptions() {
      // 同享数组与原始同享数组长度不同时，即同时包含子母卡。
      // 即返回子母卡
      if (this.shareList.length !== this.shareOptions.length) {
        return this.shareList.filter((it) =>
          ["motherCard", "value"].includes(it.name)
        );
      }
      return [];
    },
    isEmpty() {
      return this.conflictList.length + this.shareList.length === 0;
    },
  },
  watch: {
    isEmpty(now, old) {
      if (old) {
        if (this.conflictList.length === 1) {
          const key = this.conflictList[0].name;
          this.$emit("input", { ...this.value, [key]: true });
        }
      }
    },
  },
};
</script>

<style lang="less" rel="stylesheet/less">
.discount {
  height: 100%;
  background-color: #fff;
  border-radius: @border-radius-middle;

  .discount-empty {
    height: 364px;

    img {
      margin-bottom: 15px;
    }

    p {
      color: @text-color-gray;
    }
  }

  .discount-panel {
    font-size: @font-size-default;
    padding: 5px 16px 5px;

    p {
      white-space: nowrap;
    }

    .label {
    }

    .sub-value,
    .sub-label {
      color: @text-color-gray;
      font-size: @font-size-mini;
    }

    .sub-label {
      margin-left: 5px;
    }

    .sub-value {
      margin-right: 5px;
    }

    svg {
      cursor: pointer;
      font-size: 20px;
    }

    .description-icon {
      margin-left: 5px;
      color: @active-color;
    }

    .caller {
      color: @active-color;
      margin: 0 5px;
      svg {
        font-size: 14px;
      }
    }
  }

  .share-discount {
    height: 40px;

    .value {
      font-size: @font-size-small;
    }
  }

  .conflict-title {
    line-height: 30px;
    margin-bottom: 10px;
  }

  .conflict-discount {
    border: 1px solid #e5e5e5;
    margin-bottom: 10px;
    height: 44px;
    padding: 0 15px;
    border-radius: @border-radius-middle;
    cursor: pointer;

    .value,
    .sub-value {
      font-size: @font-size-small;
    }

    &.picked {
      border-color: @active-color;

      svg {
        color: @active-color;
      }
    }

    svg {
      margin-right: 6px;
      color: @text-color-gray;
    }
  }

  .add-value-btn {
    padding: 4px 8px;
    margin: 0 5px;
    color: #fd5451;
    border-color: #fd5451;
    background-color: #fff4f3;
  }

  .value-gas {
    color: #6596e1;
  }
}
</style>
