<template>
  <transition name="draw">
    <div
      @click="notice"
      style="z-index: 0; width: 37.5%;"
      class="paid-success"
      v-show="localShow"
    >
      <header class="paid-head">
        <i class="el-icon-success head-icon"></i>
        <p class="title">支付成功</p>
        <p class="price" v-if="payData">{{ payData.totalMoney | fixed }}</p>
      </header>
      <section class="paid-content" v-if="data">
        <div class="flex-panel">
          <span class="label">订单金额</span>
          <span class="value">{{ payData.originalTotalMoney | fixed }}</span>
        </div>
        <div
          class="flex-panel discount-item"
          v-for="it in discountList"
          :key="it.key"
        >
          <span class="label">{{ it.label }}</span>
          <span class="value">{{ it.value | fixed }}</span>
        </div>
        <div class="flex-panel" v-if="thirdPay">
          <span class="label">{{ thirdPay.label }}</span>
          <span class="value">{{ thirdPay.value | fixed }}</span>
        </div>
      </section>
      <section class="paid-section" v-if="data">
        <div class="flex-panel">
          <span class="label">单据号</span>
          <span class="value">{{ payData.billNumber }}</span>
        </div>
        <div class="flex-panel">
          <span class="label">支付时间</span>
          <span class="value">{{ payData.operateTime }}</span>
        </div>
      </section>

      <section class="print-block" v-if="data" @click.stop>
        <el-button type="primary" @click.stop="noticePrint">打印小票</el-button>
        <p style="padding: 10px;">
          <el-checkbox :value="shouldPrint" @input="setPrint"
            >自动打印小票</el-checkbox
          >
        </p>
      </section>
    </div>
  </transition>
</template>

<script>
export default {
  name: "PaidSuccess",
  props: {
    show: Boolean,
    data: Object,
    size: String,
  },
  components: {},
  data() {
    return {
      shouldPrint: !!JSON.parse(
        localStorage.getItem(this.$const.PRINT_STORAGE_KEY)
      ),
    };
  },
  created() {},
  methods: {
    noticePrint() {
      const parent = window.top;
      if (parent === window) throw "嵌入一卡易中才能打印!";
      if (!parent.EVENT_BUS) throw "父页面需要提供事件处理器";
      parent.EVENT_BUS.emit("print-gas-checkout", {
        ...this.data,
      });
    },

    setPrint(v) {
      localStorage.setItem(this.$const.PRINT_STORAGE_KEY, JSON.stringify(v));
      this.shouldPrint = v;
    },

    notice() {
      this.$message({
        type: "warning",
        message: "请选择油品!",
      });
    },
  },
  computed: {
    localShow: {
      get() {
        return this.show;
      },
      set(v) {
        this.$emit("update:show", v);
      },
    },

    discountList() {
      const data = this.payData;
      if (!data) return [];
      let list = [
        {
          label: "会员优惠",
          key: "discount",
          value: data.paidDiscount,
        },
        {
          label: "收款优惠",
          key: "preferentialMoney",
          value: data.preferentialMoney,
        },
        {
          label: "活动满减",
          key: "activity",
          value: data.paidFullCut,
        },
        {
          label: "积分抵扣",
          key: "point",
          value: data.paidPoint,
        },
        {
          label: "优惠券",
          key: "coupon",
          value: data.paidCoupon,
        },
        {
          label: "储值支付",
          key: "value",
          value: data.paidValue,
        },
      ];
      const paidLockDecrease = data.paidOilDecrease + data.paidLockOilPrice;
      if (data.paidOilDecrease > 0 && data.paidLockOilPrice === 0) {
        const item = {
          label: "每升减",
          key: "decrease",
          value: paidLockDecrease,
        };
        list.push(item);
      } else if (data.paidOilDecrease === 0 && data.paidLockOilPrice !== 0) {
        const item = {
          label: "锁油价优惠",
          key: "lockPrice",
          value: paidLockDecrease,
        };
        list.push(item);
      } else if (paidLockDecrease > 0) {
        const item = {
          label: "每升减",
          key: "decrease",
          value: paidLockDecrease,
        };
        list.push(item);
      } else if (paidLockDecrease < 0) {
        const item = {
          label: "锁油价优惠",
          key: "lockPrice",
          value: paidLockDecrease,
        };
        list.push(item);
      }
      return list.filter((it) => +it.value.toFixed(2) !== 0);
    },
    thirdPay() {
      if (!this.data) return null;
      const data = this.payData;
      if (data.paidThirdPay)
        return {
          label: data.thirdPayType,
          value: data.paidThirdPay,
        };
      if (data.paidMoney)
        return {
          label: "现金",
          value: data.paidMoney,
        };
      if (data.paidOther)
        return {
          label: data.otherPayType,
          value: data.paidOther,
        };
      if (data.paidCard)
        return {
          label: "银行卡",
          value: data.paidCard,
        };
    },

    payData() {
      if (!this.data) return null;
      return this.data.gasConsumeSuccessEntity;
    },

    paytype() {
      if (!this.payData) return "";
      return this.payData.thirdPayType || this.payData.otherPayType;
    },
  },
};
</script>

<style lang="less" rel="stylesheet/less">
.draw-enter,
.draw-leave-to {
  transform: translate(100%);
}

.draw-enter-active,
.draw-leave-active {
  transition: 0.3s;
}

.paid-success {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

  padding: 0 20px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;

  .el-drawer__body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0;
  }

  .paid-head {
    text-align: center;

    .head-icon {
      font-size: 100px;
      color: #00c250;
      margin-bottom: 10px;
    }

    .title {
      color: #00c250;
      font-size: @font-size-normal;
      margin-bottom: 15px;
    }

    .price {
      color: @text-color-dark;
      font-size: @font-size-huge;
      font-weight: bold;

      &:before {
        content: "￥";
        font-size: @font-size-big;
      }
    }
  }

  section {
    padding: 30px 0;
    margin: 0 30px;
    font-size: @font-size-default;

    .flex-panel {
      line-height: 2;
    }
  }

  .paid-content {
    border-bottom: 1px dashed #e5e5e5;

    .value {
      font-weight: bold;

      &:before {
        content: "￥";
      }
    }
  }

  .paid-section {
    .label {
      color: @text-color-gray;
    }

    .value {
      color: @text-color-black;
    }
  }

  .print-block {
    text-align: center;

    button {
      width: 180px;
    }
  }

  .discount-item {
    color: @price-color;
    font-size: @font-size-small;
  }
}
</style>
