<template>
  <div class="gas-panel shadow-normal">
    <div class="panel">
      <div class="flex-panel">
        <p>
          <span class="label">油品:</span>
          <span class="value text-yellow" v-if="currentGasItem"
            >{{ currentGasItem.gunName }} {{ currentGasItem.gsName }}</span
          >
        </p>
        <p v-if="currentGasItem && gasPrice !== 1">
          (￥{{ gasPrice }}/{{ currentGasItem.unit || "升" }})
        </p>
      </div>

      <div>
        <p v-if="gasPrice !== 1">
          <span class="label">共计:</span>
          <span class="value" v-if="currentGasItem"
            >{{ totalUnit }}{{ currentGasItem.unit || "升" }}</span
          >
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "GasPanel",
  components: {},
  data() {
    return {};
  },
  created() {},
  methods: {},
  computed: {
    gasPrice() {
      if (this.currentGasItem) {
        return this.currentGasItem?.isBargain
          ? this.currentGasItem.bargainPrice
          : this.currentGasItem.price;
      }
      return 1;
    },
    ...mapGetters("Main", ["currentGasItem", "totalUnit"])
    /*    currentGas() {
      return this.$store.getters["Main/currentGasItem"] || null;
    },
    totalUnit() {
	  return this.$store.getters["Main/totalUnit"]
    }*/
  }
};
</script>

<style lang="less" rel="stylesheet/less">
.gas-panel {
  background-color: #fff;
  font-size: @font-size-default;
  border-radius: @border-radius-middle;

  .panel {
    padding: 15px;

    .flex-panel {
      margin-bottom: 10px;
    }

    .label {
      color: @text-color-gray;
      margin-right: 10px;
    }
  }
}
</style>
