<template>
  <el-popover
    class="coupon-pop"
    v-model="localShow"
    placement="bottom-start"
    trigger="click"
    :width="width"
  >
    <ul class="coupon-list">
      <li
        class="coupon-item flex-panel"
        v-for="it in data"
        :key="it.couponSendNoteGuid"
      >
        <span class="name">{{ it.name }}</span>
        <span class="name">&times;{{ it.number }}</span>
      </li>
    </ul>
    <span class="caller" slot="reference" @click.stop
      ><svg-icon name="notice"
    /></span>
  </el-popover>
</template>

<script>
export default {
  name: "CouponPop",
  props: {
    data: Array,
    show: Boolean
  },
  components: {},
  data() {
    return {
      width: 300,
      offset: 250
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.width = this.$parent.$el.clientWidth;
    });
  },
  methods: {},
  computed: {
    localShow: {
      get() {
        return this.show;
      },
      set(v) {
        this.$emit("update:show", v);
      }
    }
  }
};
</script>

<style lang="less" rel="stylesheet/less">
.coupon-pop {
}

.coupon-list {
  margin: 0;
  padding: 0;
  max-height: 200px;
  overflow: auto;

  .coupon-item {
    list-style: none;
    padding: 10px;
  }
}
</style>
