<template>
  <div id="app">
    <el-row type="flex">
      <el-col :span="5" style="position: relative; z-index: 1;">
        <nav class="page-left ignore">
          <GunPicker v-model="oilIndexGuid" :options="gunList" ref="gun" />
        </nav>
      </el-col>
      <el-col :span="10">
        <section class="page-center">
          <div class="main">
            <div class="main-top">
              <MemberReader
                @focus="readerFocus"
                style="margin-bottom: 12px;"
                :is-focus="focusedElem === $refs['member']"
                v-model="memberCardId"
                :disabled="pageDisabled"
                :data="memberData"
                ref="member"
              />
              <GasPanel style="margin-bottom: 15px;" ref="gas" />
              <PriceField
                @focus="priceFocus"
                :is-focus="focusedElem === $refs['price']"
                v-model="orderMoney"
                :disabled="priceDisabled"
                ref="price"
              />
            </div>
            <div class="main-bottom">
              <Keyboard
                v-model="keyboardValue"
                :focused-elem="focusedElem"
                :disabled="keyboardDisabled"
                free
                ref="keyboard"
              />
            </div>
          </div>
        </section>
      </el-col>
      <el-col :span="9">
        <section class="page-right">
          <div class="main">
            <div class="main-top">
              <Discount
                v-model="discountCheckMap"
                :disabled="pageDisabled"
                ref="discount"
              />
            </div>

            <div class="main-bottom">
              <PropertyValidator
                class="main-bottom-item"
                v-model="valueCheckCode"
                :disabled="pageDisabled"
                :show="isNeedValidPassword"
                @focus="propertyValidatorFocus"
                :is-focus="focusedElem === $refs['member']"
                ref="value"
              />

              <div class="rest-value shadow-normal flex-panel main-bottom-item">
                <b class="label">还需支付:</b>
                <span class="value text-red"
                  ><i>￥</i>{{ formatRestMoney }}</span
                >
              </div>

              <CashField
                v-model="depositedMoney"
                :rest-money="formatRestMoney"
                :cash.sync="paidCash"
                :changeMoney.sync="changeMoney"
                :show="cashFieldShow"
              />
              <PaytypePicker
                :disabled="thirdPayDisabled"
                :options="thirdPaytype"
                v-model="pickedThirdPaytype"
                class="main-bottom-item"
                ref="paytype"
                @pick="pickPaytype"
              />

              <Memo
                v-model="memo"
                :disabled="pageDisabled"
                ref="memo"
                class="main-bottom-item"
              />

              <div class="confirm-btn">
                <button
                  class="flex-center shadow-press"
                  @click="checkout"
                  :disabled="checkoutDisabled"
                >
                  <span>
                    <svg-icon name="closing" />
                    <b>结账</b>
                  </span>
                </button>
              </div>
            </div>
          </div>
        </section>
      </el-col>
      <div class="page-mask" v-if="pageDisabled" @click="clickMask"></div>
      <PaidSuccess :show.sync="resultShow" :data="paidData" />
    </el-row>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
import Watcher from "@/utils/size-watcher";
import PayOrder from "@/mixins/PayOrder";
import getPaymentCode from "@/services/getPaymentCode";
import { onParentNotice, paymentSucceed, paymentFailed } from "@/utils/ext";

const lowCase = (str) => str.replace(/^(.)/, (a, b) => b.toLowerCase());
const MAX_LOOP_COUNT = 30; // 最大轮询次数

export default {
  name: "APP",

  data() {
    return {
      orderMoney: "",
      memo: "",
      resultShow: false,
      valueCheckCode: "", // 储值校验密码
      pickedThirdPaytype: "", // 选择的第三方支付方式
      depositedMoney: 0, // 存入储值的零钱,勾选了有值,不勾选为0
      changeMoney: 0, //找零金额
      paidCash: 0,
      cashFieldShow: false,

      // 当前接受键盘输入的组件对象,必须提供[getValue][setValue]方法,需要控制光标则需要提供[getFocusIndex]方法
      focusedElem: null,
      // 支付成功返回的数据
      paidData: null,
      isAutoCheck: false,
    };
  },
  mixins: [PayOrder],
  async created() {
    await this.init();
    this.autoCheck();
    // this.loopRequestOrders(1500);
    // 父页面通知时,更新订单
    this.listenParentNotice();
  },

  methods: {
    // region 初始化
    async init() {
      // 每升减需要发送请求,而金额是高频变动参数,提供一个防抖的更新方法
      this.deboucedDispatch = this.$utils.debounce(
        this.dispatchCompute.bind(this),
        300
      );
      this.sizeWatcher && this.sizeWatcher.destroy();
      // 监听页面大小的变动
      this.sizeWatcher = new Watcher();
      this.sizeWatcher.on("resize", (e) => {
        this.$store.commit("App/RESIZE", e);
      });
      await this.getPageData();
    },

    autoCheck() {
      const { goodsitemguid = "", cardid = "" } = this.$utils.param2Obj(
        location.href
      );

      let index = this.gunList.findIndex(
        (x) => x.goodsItemGuid === goodsitemguid
      );
      if (index > -1) {
        this.oilGunGuid = this.gunList[index].guid;
        this.oilIndexGuid = this.gunList[index].id;
        this.memberCardId = cardid;
      }
      this.isAutoCheck = true;
    },

    // 获取商家配置及油枪等固定信息
    async getPageData() {
      await this.$store.dispatch("Main/getPageData");
    },
    // endregion

    // region 命令
    // 获取会员信息及其优惠条件
    async getMemberInfo() {
      if (!this.goodsItemGuid) return;
      const result = await this.$store.dispatch("Main/getMemberInfo");
      if (!result.success) return;
      this.focusOnElement();
    },

    // 油枪,会员变动后聚焦正确的元素
    focusOnElement() {
      const member = this.$refs["member"];
      const price = this.$refs["price"];
      if (this.noPayOrder) {
        price.setValue(this.noPayOrder.money);
        // console.log("聚焦会员");
        this.$nextTick(() => {
          price?.setBlur();
          member?.setFocus();
        });
      } else {
        // console.log("聚焦金额");
        this.$nextTick(() => {
          member?.setBlur();
          price?.setFocus();
        });
      }
    },

    // 金额,会员,油品变动,触发dispatch更新优惠抵扣
    computeDiscount(change) {
      // 如果优惠项目包含储值,防抖计算
      if (this.isNeedDelayRequest && change.changByPrice) {
        return this.deboucedDispatch(change);
      }
      this.dispatchCompute(change);
    },

    // 更新优惠信息
    async dispatchCompute(change) {
      await this.$store.dispatch("Main/computeDiscount", {
        money: this.orderMoney,
        change,
      });
      if (this.formatRestMoney === 0) {
        this.pickedThirdPaytype = "";
      }
    },

    restore() {
      this.$nextTick(() => {
        this.goodsItemGuid = "";
        this.oilGunGuid = "";
        this.oilIndexGuid = "";
      });
      this.memberCardId = "";
      this.orderMoney = "";
      this.$store.commit("Main/RESET_DISCOUNT_CHECK_MAP");
      this.pickedThirdPaytype = "";
      this.valueCheckCode = "";
      this.$store.commit("Main/SET_MEMBER_DATA", null);
      this.$store.commit("Main/SET_PICKED_COUPONS", []);
      this.$refs["member"].restore();
    },

    // 轮询订单接口
    async loopRequestOrders(time) {
      while (this.isNeedLoopRequest) {
        await this.$store.dispatch("Main/getOrderList");
        await this.$utils.sleep(time);
      }
    },

    // 等待父页面通知获取订单
    listenParentNotice() {
      if (!this.isNeedLoopRequest) return;
      this.$store.dispatch("Main/getOrderList");
      onParentNotice(async () => {
        await this.$store.dispatch("Main/getOrderList");
      });
    },

    // endregion

    // region 事件回调
    // 点击透明蒙层
    clickMask() {
      // 该蒙层在未选择油品时出现
      this.$message({
        type: "warning",
        message: "请选择油品!",
      });
    },

    pickPaytype(type) {
      if (type !== "cash") {
        return (this.cashFieldShow = false);
      }
      this.cashFieldShow = !this.cashFieldShow;
    },

    async scanCode() {
      if (this.pickedThirdPaytype !== "mobile") return null;
      if (this.formatRestMoney === 0) return null;
      const result = await getPaymentCode({
        money: this.formatRestMoney,
        enabledPayWay: this.enabledPayWay,
      });
      if (!result.success) {
        this.$message({
          type: "error",
          message: result.message || "扫码失败!",
        });
        result.vm && (result.vm.show = false);
      }
      return result;
    },

    async checkout() {
      const payment = await this.scanCode();
      if (payment && !payment.success) return;
      console.log("扫码结果: ", payment);
      if (this.onPaying) return;
      this.onPaying = true;
      const result = await this.payOrder({
        dynamicId: payment?.data.paymentCode ?? "",
        thirdPayType: lowCase(payment?.data.thirdPayWay ?? ""),
        paidThirdPay: payment?.data.paidThirdPay ?? 0,
      });
      this.onPaying = false;

      if (!result.success) {
        payment && payment.vm && (payment.vm.show = false);
        paymentFailed();
        return;
      }

      if (result.data.orderStatus === 0) {
        //需要查单
        await this.getOrderStatus(result.data.orderNumber, MAX_LOOP_COUNT);
        payment && payment.vm && (payment.vm.show = false);
        return;
      }
      payment && payment.vm && (payment.vm.show = false);
      console.log("支付成功!");
      this.restore();
      this.cashFieldShow = false;
      this.paidData = result.data;
      this.resultShow = true;
      paymentSucceed(result.data); // 通知父页面支付成功
      this.noticePrint(result.data);
    },

    noticePrint(data) {
      if (window.parent === window) return;
      const shouldPrint = !!JSON.parse(
        localStorage.getItem(this.$const.PRINT_STORAGE_KEY)
      );
      if (!shouldPrint) return;
      console.log("通知打印 >>>>>>>>>>>> notice print");
      window.parent?.YkyNewPrintUrl(
        "Print_Consume",
        data?.gasConsumeSuccessEntity?.billNumber ?? ""
      );
    },

    // endregion

    // region 接受键盘输入
    priceFocus({ el }) {
      this.focusedElem = el;
    },
    readerFocus({ el }) {
      this.focusedElem = el;
    },
    propertyValidatorFocus({ el }) {
      this.focusedElem = el;
    },
    // endregion
  },

  computed: {
    // 未选择油品时,禁止所有交互行为
    pageDisabled() {
      return this.goodsItemGuid === "";
    },

    // 选择了油机同步金额时,禁止输入金额
    priceDisabled() {
      return this.pageDisabled || this.noPayOrder !== null;
    },

    keyboardDisabled() {
      return (
        this.pageDisabled ||
        (this.priceDisabled && this.focusedElem === this.$refs["price"])
      );
    },

    thirdPayDisabled() {
      // 没有余额待付则禁用支付方式
      return this.pageDisabled || +this.formatRestMoney === 0;
    },

    checkoutDisabled() {
      if (
        this.pickedThirdPaytype === "cash" &&
        this.paidCash < this.formatRestMoney
      )
        return true;

      return (
        this.pageDisabled ||
        +this.orderMoney === 0 ||
        (this.isNeedValidPassword &&
          (this.valueCheckCode.length < 6 ||
            !(this.valueCheckCode * 1 >= 0))) || // 需要验证密码而未输入
        (this.formatRestMoney !== 0 && this.pickedThirdPaytype === "")
      ); // 还有余款待付而未选择支付方式
    },

    // region 虚拟绑定值
    // 键盘绑定值
    keyboardValue: {
      // 取值来自于当前键盘接受组件
      // 设置也设置到当前键盘组件
      get() {
        if (!this.focusedElem) return "";
        return this.focusedElem?.getValue() ?? 0;
      },
      set(v) {
        if (!this.focusedElem) return;
        this.focusedElem?.setValue(v);
      },
    },

    goodsItemGuid: {
      set(v) {
        this.$store.commit("Main/SET_GOODS_ITEM_GUID", v);
      },
      get() {
        return this.$store.state.Main.goodsItemGuid;
      },
    },
    oilGunGuid: {
      set(v) {
        this.$store.commit("Main/SET_OIL_GUN_GUID", v);
      },
      get() {
        return this.$store.state.Main.oilGunGuid;
      },
    },
    oilIndexGuid: {
      set(v) {
        this.$store.commit("Main/SET_OIL_INDEX_GUID", v);
      },
      get() {
        return this.$store.state.Main.oilIndexGuid;
      },
    },

    discountCheckMap: {
      set(v) {
        this.$store.commit("Main/SET_DISCOUNT_CHECK_MAP", v);
      },
      get() {
        return this.$store.state.Main.discountCheckMap;
      },
    },
    memberCardId: {
      set(v) {
        this.$store.commit("Main/SET_MEMBER_CARD_ID", v);
      },
      get() {
        return this.$store.state.Main.memberCardId;
      },
    },

    // endregion

    // region 映射store
    ...mapState("App", {
      HEIGHT: "height",
    }),
    ...mapState("Main", ["gunList", "memberData", "noPayOrder"]),
    ...mapGetters("Main", [
      "thirdPaytype",
      "isNeedDelayRequest",
      "isNeedLoopRequest",
      "formatRestMoney",
      "isNeedValidPassword",
      "enabledPayWay",
    ]),
    // endregion
  },

  watch: {
    // region 更新优惠方式的监视器
    orderMoney() {
      this.$store.commit("Main/SET_PICKED_COUPONS", []);
      this.$store.commit("Main/SET_ORDER_MONEY", this.orderMoney);
      this.computeDiscount({ changByPrice: true });
    },
    memberData() {
      this.$store.commit("Main/SET_PICKED_COUPONS", []);
      this.computeDiscount({});
    },
    // 更换勾选状态,重新计算
    discountCheckMap(now, old) {
      this.$store.commit("Main/SET_PICKED_COUPONS", []);
      this.computeDiscount({ changeByCheck: true });
    },

    // endregion

    oilIndexGuid(now, old) {
      this.$store.commit("Main/SET_PICKED_COUPONS", []);
      if (now) {
        let gun = this.gunList.find((x) => x.id === now);
        this.oilGunGuid = gun.guid;
        if (gun.disable) {
          this.$message({
            type: "warning",
            message: gun.reason,
          });

          let oldgun = this.gunList.find((x) => x.id === old);
          if (oldgun) {
            this.oilGunGuid = oldgun.guid;
          }
          this.oilIndexGuid = old;
          return;
        }
        if (
          (gun.isBargain && gun.bargainPrice === 0) ||
          (!gun.isBargain && gun.price === 0)
        ) {
          this.$message({
            type: "warning",
            message: "油品售价不能为0!",
          });
          let oldgun = this.gunList.find((x) => x.id === old);
          if (oldgun) {
            this.oilGunGuid = oldgun.guid;
          }
          this.oilIndexGuid = old;
          return;
        }
        this.goodsItemGuid = gun.goodsItemGuid;
      }
    },

    // 监视支付方式的勾选
    formatRestMoney(now) {
      if (now === 0) {
        this.pickedThirdPaytype = "";
      }
    },

    noPayOrder(now) {
      this.focusOnElement();
    },

    // region 获取会员信息的监视器
    async goodsItemGuid(now, old) {
      if (!old) {
        this.$refs["price"]?.setFocus();
        this.resultShow = false;
        this.paidData = null;
      }
      await this.getMemberInfo();
      this.focusOnElement();
      this.computeDiscount({});
    },
    async memberCardId() {
      await this.getMemberInfo();
      this.focusOnElement();
    },
    // endregion
  },
};

function isSame(a, b) {
  if (a === b) return true;
  for (var key in a) {
    if (a[key] !== b[key]) return false;
  }
  return true;
}
</script>

<style lang="less">
#app {
  height: 100vh;
  min-height: 640px;
  background-color: #f7f7f7;
  overflow-y: hidden;
  overflow-x: auto;

  color: @text-color-primary;

  .page-mask {
    position: absolute;
    bottom: 0;
    right: 0;
    top: 0;
    left: 0;
  }

  > .el-row {
    > .el-col {
      > nav,
      > section {
        height: 100vh;
        min-height: 640px;
        margin-right: 16px;
      }
    }
  }

  .main {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 16px 0;
    overflow: auto;
  }

  .page-left {
    background-color: #fff;
    margin-right: 16px;
    position: relative;
    z-index: 10;
  }

  .page-center {
    .main-top {
      height: 50%;
      display: flex;
      flex-direction: column;
    }

    .main-bottom {
      height: 50%;
      margin-bottom: -10px;
    }
  }

  .page-right {
    margin-right: 0;
    .main-top {
      flex: 1;
      padding-bottom: 12px;
    }

    .main-bottom-item {
      margin-bottom: 12px;
    }
  }
}

.rest-value {
  background-color: #fff;
  border-radius: @border-radius-middle;
  height: 56px;
  padding: 0 16px;

  i {
    font-style: normal;
    font-weight: normal;
    font-size: @font-size-default;
  }

  .value {
    font-weight: bold;
    font-size: @font-size-great;
  }
}

.confirm-btn {
  button {
    border: 0;
    width: 100%;
    height: 88px;
    cursor: pointer;
    user-select: none;
    color: @text-color-white;
    border-radius: @border-radius-middle;
    background: linear-gradient(90deg, #fd8051, #fc3b39);
  }

  span {
    display: flex;
    align-items: center;
  }

  b {
    font-size: @font-size-great;
  }

  svg {
    font-size: 40px;
    margin-right: 5px;
  }
}
</style>
