<template>
  <div class="paytype-picker shadow-normal" :class="{ disabled }">
    <div
      v-for="item in options"
      :key="item.name"
      :class="item.name === 'other' ? 'other-col' : 'normal-col'"
    >
      <div
        class="paytype-item flex-center"
        :class="getItemClass(item)"
        :title="item.name === 'mobile' ? mobilePayWay.join(',') : ''"
        @click="pickItem(item)"
      >
        <template v-if="item.name !== 'other'">
          <svg-icon :name="'pay_' + item.name" />
          <p>{{ item.label }}</p>
        </template>
        <template v-else>
          <p class="other-paytype" v-html="display(otherPaytypeName)"></p>
          <i class="split-line"></i>
          <el-popover v-model="popShow" placement="top-end" width="170">
            <ul class="pop-list">
              <li
                class="pop-item"
                v-for="it in item.subOptions"
                @click="pickSubItem(it)"
                :class="it.name === value ? 'picked' : ''"
                :key="it.name"
              >
                {{ it.label }}
              </li>
            </ul>
            <p class="caller" slot="reference" @click.stop>
              <i :class="popShow ? '' : 'turn_up'">
                <svg-icon name="arrow_down" />
              </i>
            </p>
          </el-popover>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";

export default {
  name: "PaytypePicker",
  props: {
    options: Array,
    value: String,
    disabled: Boolean,
  },
  components: {},
  data() {
    return {
      popShow: false,
      pickedOtherPaytype: "",
    };
  },
  created() {},
  methods: {
    display: (v) => {
      return v.substring(0, 4).replace(/^(..)/, "$1<br>");
    },
    getItemClass(item) {
      if (item.name !== "other") {
        return item.name + (item.name === this.value ? " picked" : "");
      } else {
        return (
          item.name + (this.otherPaytypeName === this.value ? " picked" : "")
        );
      }
    },
    pickSubItem(item) {
      this.$nextTick(() => {
        this.$emit("input", item.name);
      });
      this.popShow = false;
      this.pickedOtherPaytype = item.name;
    },
    pickItem(item) {
      if (this.disabled) return;
      let name = item.name;
      if (name === "other") {
        name = this.otherPaytypeName;
      }
      /*      if (this.value === name) {
                name = "";
              }*/
      this.$emit("pick", name);
      this.$emit("input", name);
    },
  },
  computed: {
    otherPaytypeName() {
      const other = this.options.find((it) => it.name === "other");
      return this.pickedOtherPaytype || other.subOptions[0].name || "其他";
    },
    ...mapGetters("Main", ["enabledPayWay"]),
    mobilePayWay() {
      const result = [];
      const map = this.enabledPayWay;
      if (map.isAliPayEnabled) result.push("支付宝");
      if (map.isWeChatPayEnabled) result.push("微信");
      if (map.isUnionPayEnabled) result.push("云闪付");
      if (map.isBestPayEnabled) result.push("翼支付");
      return result;
    },
  },
  watch: {
    disabled(now, old) {
      if (now) {
        this.$emit("input", "");
      }
    },
  },
};
</script>

<style lang="less" rel="stylesheet/less">
.paytype-picker {
  border-radius: @border-radius-middle;
  background-color: #fff;
  padding: 12px 14px;

  display: flex;
  justify-content: space-between;

  .normal-col {
    width: 20%;
  }

  .other-col {
    width: 22.21%;
    position: relative;

    .paytype-item {
      flex-direction: row;
      justify-content: space-between;

      position: absolute;
      bottom: 0;
      right: 0;
      top: 0;
      left: 0;

      .other-paytype {
        display: inline-block;
        width: 2em;
        margin-right: 6px;
      }

      .split-line {
        display: block;
        height: 1.4em;
        border-left: 1px solid #e5e5e5;
      }

      .caller {
        position: relative;

        &:before {
          content: "";
          position: absolute;
          bottom: -20px;
          right: -10px;
          top: -20px;
          left: 0;
        }

        i {
          display: inline-block;
          transition: 0.2s;

          svg {
            font-size: 16px;
            margin: 0 3px;
          }
        }

        .turn_up {
          transform: rotate(180deg);
        }
      }
    }
  }

  .paytype-item {
    text-align: center;
    border: 1px solid #e5e5e5;
    background-color: #f8f8f8;
    border-radius: @border-radius-middle;
    cursor: pointer;
    user-select: none;

    &.cash {
      svg {
        color: #ff9917;
      }
    }

    &.bank {
      svg {
        color: #3889ff;
      }
    }

    &.mobile {
      svg {
        color: #1bb987;
      }
    }

    &.picked {
      background-color: @active-color;
      color: @text-color-white;
      border-color: @active-color;

      svg {
        color: @text-color-white;
      }
    }

    p {
      white-space: nowrap;
      padding-bottom: 4px;
    }

    svg {
      font-size: 50px;
    }
  }
}

.pop-list {
  list-style: none;
  padding: 0;
  margin: 0 -12px;

  .pop-item {
    height: 48px;
    line-height: 48px;
    white-space: nowrap;
    text-align: center;
    cursor: pointer;
    font-size: @font-size-default;

    &.picked {
      background-color: #ebf0f8;
    }
  }
}

.w-100-100 {
  aspect-ratio: "100:100";
}

.w-110-100 {
  aspect-ratio: "110:100";
}

@media screen and (min-width: 1200px) {
  .other-col {
    .paytype-item {
      justify-content: center !important;

      .other-paytype {
      }

      .split-line {
        margin: 0 10px;
      }
    }
  }
}
</style>
