<template>
  <div class="member-reader shadow-normal" :class="{ 'is-focus': isFocus }">
    <form action="" @submit="readCard">
      <div class="flex-panel read-panel">
        <input
          type="text"
          :disabled="disabled"
          @focus="inputFocus"
          v-model="cardId"
          autocomplete="off"
          placeholder="请输入会员卡号/手机号"
          tabindex="2"
          ref="input"
        />
        <button
          class="read-btn shadow-press"
          :disabled="disabled"
          type="submit"
        >
          读卡
        </button>
        <button
          class="cancel-btn shadow-press"
          :disabled="disabled"
          @click="removeMember"
        >
          清除
        </button>
      </div>
    </form>

    <transition name="grow">
      <div class="flex-panel member-panel" v-if="data">
        <p>
          <span class="label">姓名:</span>
          <span class="text-blue"
            >{{ data.trueName }}
            <i @click="showMember"><svg-icon name="notice" /></i
          ></span>
        </p>
        <p>
          <span class="label">卡号:</span>
          <span class="">{{ data.cardId }}</span>
        </p>
      </div>
    </transition>

    <MemberAction :data="data" v-if="data" :show.sync="actionShow" />
  </div>
</template>

<script>
import pickMember from "@/services/getMember/main";
import * as API from "api";
import MemberAction from "./MemberAction";

export default {
  name: "MemberReader",
  props: {
    value: String,
    disabled: Boolean,
    isFocus: Boolean,
    data: Object,
  },
  components: { MemberAction },
  data() {
    return {
      cardId: "",
      actionShow: false,
    };
  },
  created() {},
  methods: {
    getFocusIndex() {
      return (this.__focusIndex__ = this.$refs?.input?.selectionStart ?? 0);
    },
    inputFocus(e) {
      this.focus();
    },

    focus() {
      this.$emit("focus", { el: this });
    },

    setFocus() {
      this.$refs?.input.focus();
      this.focus();
    },

    setBlur() {
      this.$refs?.input.blur();
    },

    setValue(v) {
      console.log("set member reader value ", v);
      const input = this.$refs?.input;
      let index = this.__focusIndex__ || input.value.length;
      index += v.length - this.cardId.length;
      this.cardId = v;
      this.$nextTick(() => {
        if (!input) return;
        input.selectionStart = index;
        input.selectionEnd = index;
      });
    },

    getValue() {
      return this.cardId;
    },

    restore() {
      this.cardId = "";
    },

    removeMember() {
      console.log("remove ");
      this.restore();
      this.$emit("input", "");
    },

    async readCard(e) {
      e.preventDefault();
      if (!this.cardId) return;
      const result = await API.checkMember(this.cardId);
      if (!result.success) return;
      let trueCardId = result.data.trueCardId;
      // 多个会员,弹窗选择
      if (!trueCardId) {
        const list = await API.getMemberList(this.cardId);
        if (!list.success) return;
        const options = list.data.memberList.map((it) => {
          it.disabled = it.isLocked || it.isExpire;
          if (it.disabled) {
            it.title = "该会员已被锁定或已过期!";
          }
          return it;
        });
        const picked = await pickMember({ options });
        trueCardId = picked.data.cardId;
      }
      this.$emit("input", trueCardId);
    },

    showMember() {
      this.actionShow = true;
    },
  },
  computed: {},
  watch: {
    value: {
      async handler() {
        if (!this.$refs["input"]) {
          await this.$utils.sleep(100);
        }

        if (this.$refs["input"].value !== this.value) {
          this.cardId = this.value;
          this.$refs["input"].value = this.value;
        }
      },
      immediate: true,
    },
  },
};
</script>

<style lang="less" rel="stylesheet/less">
.member-reader {
  background-color: #fff;
  font-size: @font-size-default;
  border-radius: @border-radius-middle;
  padding: 15px 22px;

  &.is-focus {
    .read-panel input {
      border-color: @active-color;
    }
  }

  .read-panel {
    input {
      flex: 1;
      margin-right: 20px;
      height: 54px;
      border-radius: @border-radius-middle;
      border: 1px solid #e5e5e5;
      padding: 0 10px;

      &:focus {
        border-color: @active-color;
      }
    }

    .read-btn {
      display: inline-block;
      width: 97px;
      height: 54px;
      border: none;
      border-radius: @border-radius-middle;
      background-color: @active-color;
      color: @text-color-white;
      margin-right: 20px;
    }

    .cancel-btn {
      height: 54px;
      width: 80px;
      border: 1px solid #e5e5e5;
      background-color: #f7f7f7;
      border-radius: @border-radius-middle;
    }
  }

  .member-panel {
    height: 40px;
    overflow: hidden;

    p {
      padding-top: 15px;
      flex: 1;
    }

    .label {
      color: @text-color-gray;
      margin-right: 15px;
    }

    svg {
      cursor: pointer;
    }
  }

  .grow-enter,
  .grow-leave-to {
    height: 0;
  }

  .grow-enter-active,
  .grow-leave-active {
    transition: 0.3s;
  }
}
</style>
