import EmitAble from './emit-able';
import {debounce, throttle} from './index';

export default class SizeWatcher extends EmitAble {
  constructor() {
	super();
	// this.resizeHandler = throttle(this.onResize);
	this.resizeHandler = /*debounce*/(this.onResize);
	this.unListen();
	this.listen();
	setTimeout(() => {
	  this.onResize();
	}, 20);
  }

  listen() {
	window.addEventListener('resize', this.resizeHandler);
  }

  unListen() {
	window.removeEventListener('resize', this.resizeHandler);
  }

  onResize = e => {
	const width = document.documentElement.clientWidth;
	const height = document.documentElement.clientHeight;
	this.fire('resize', {width, height});
  };

  destroy() {
	this.unListen();
  }
}
