<template>
  <el-dialog
    :visible.sync="vShow"
    title="会员信息"
    class="member-action"
    width="640px"
  >
    <div class="member-action-body">
      <el-row class="member-info" type="flex" align="middle">
        <el-col :span="6">
          <p class="label">卡号</p>
          <p class="value">{{ data.cardId }}</p>
        </el-col>
        <el-col :span="6">
          <p class="label">姓名</p>
          <p class="value">{{ data.trueName }}</p>
        </el-col>
        <el-col :span="6">
          <p class="label">手机号</p>
          <p class="value">{{ data.mobile }}</p>
        </el-col>
        <el-col :span="6">
          <p class="label">会员级别</p>
          <p class="value">{{ data.memberGroupName }}</p>
        </el-col>
      </el-row>

      <section class="member-property">
        <div class="flex-panel">
          <div class="property-panel point-panel">
            <p class="label">积分</p>
            <div class="value text">{{ data.availablePoint }}</div>
          </div>
          <div class="property-panel value-panel">
            <p class="label">储值</p>
            <div class="value">
              <el-row :gutter="10">
                <el-col
                  :span="6"
                  style="width: auto;"
                  v-if="data.oilCardInfoList && data.oilCardInfoList.length > 0"
                  v-for="it in data.oilCardInfoList"
                  :key="it.goodsItemTypeGuid"
                >
                  <p class="text">{{ it.availableValue }}</p>
                  <p class="desc">(适用{{ it.goodsItemTypeName }})</p>
                </el-col>

                <el-col
                  :span="6"
                  v-if="
                    !data.oilCardInfoList || data.oilCardInfoList.length < 1
                  "
                >
                  <p class="text">{{ data.availableValue }}</p>
                </el-col>
              </el-row>
            </div>
          </div>
        </div>

        <div class="property-panel mother-panel" v-if="data.motherCardInfo">
          <p class="label">母卡</p>
          <div class="value text">{{ availableMotherValue }}</div>
        </div>

        <div class="ext-info" v-if="data.carNoList.length">
          <div class="card-no panel">
            <svg-icon name="car_id" />
            <div class="label">
              <span>车牌号</span>
            </div>
            <div class="value">
              <span v-for="it in data.carNoList" :key="it">{{ it }}</span>
            </div>
          </div>
        </div>
      </section>

      <footer>
        <button class="confirm-btn shadow-press" @click="close">确认</button>
      </footer>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: "MemberAction",
  props: {
    show: Boolean,
    data: Object,
  },
  components: {},
  data() {
    return {};
  },
  created() {},
  methods: {
    close() {
      this.$emit("update:show", false);
    },
  },
  computed: {
    availableMotherValue() {
      const { motherCardInfo } = this.data;
      if (!motherCardInfo) return 0;
      return Math.min(
        motherCardInfo.limitValue,
        motherCardInfo.motherAvailableValue
      );
    },
    vShow: {
      get() {
        return this.show;
      },
      set(v) {
        this.$emit("update:show", v);
      },
    },
  },
};
</script>

<style lang="less" rel="stylesheet/less">
.member-action {
  .member-action-body {
    border-radius: 12px;
    background-color: #fff;
  }

  .member-info {
    background-color: @blue-color;
    height: 100px;

    .el-col {
      text-align: center;
      color: @text-color-white;
      height: 60px;

      &:not(:last-child) {
        border-right: 1px solid #ddd;
      }

      .label {
        font-size: @font-size-small;
        opacity: 0.7;
        margin-bottom: 15px;
      }

      .value {
        font-size: @font-size-default;
      }
    }
  }

  footer {
    padding: 30px;
    text-align: center;

    .confirm-btn {
      background-color: @blue-color;
      color: @text-color-white;
      width: 250px;
      height: 56px;
      border-radius: @border-radius-large;
      border: 0;
    }
  }

  .member-property {
    padding: 30px 36px 10px;

    .flex-panel {
      align-items: flex-start;
    }

    .property-panel {
      position: relative;
      padding-left: 20px;

      &:before {
        content: "";
        position: absolute;
        height: 50px;
        top: 0;
        left: 0;
        width: 4px;
      }

      p {
        white-space: nowrap;
      }

      .label {
        font-size: @font-size-small;
        color: @text-color-gray;
        margin-bottom: 10px;
      }

      .value {
        font-size: @font-size-default;
        color: @text-color-primary;
      }

      .desc {
        color: @text-color-gray;
        font-size: @font-size-micro;
      }
    }

    .ext-info {
      .panel {
        padding: 15px 0;
        display: flex;
        align-items: center;
        .label {
          width: 5em;
          font-size: 18px;
          color: @blue-color;
          margin-left: 5px;
        }
        svg {
          color: #3388e1;
          font-size: 28px;
        }
        .value {
          span {
            font-size: 24px;
            font-weight: 500;
            padding-right: 1em;
            color: #222;
          }
        }
      }
    }
  }

  .point-panel {
    &:before {
      background-color: #6fb1f3;
    }
  }

  .value-panel {
    min-width: 50%;

    &:before {
      background-color: #fc935c;
    }

    .value {
      display: flex;
    }
  }

  .mother-panel {
    &:before {
      background-color: #ff6666;
    }
  }
}

/*region modify*/
.member-action {
  .el-dialog {
    overflow: hidden;
    border-radius: @border-radius-middle;
  }

  .el-dialog__header {
    background-color: @blue-color;

    .el-dialog__title,
    .el-dialog__headerbtn .el-dialog__close {
      color: @text-color-white;
    }
  }

  .el-dialog__body {
    padding: 0;
  }
}

/*endregion*/
</style>
