export default function validate(paymentCode, global) {
  if (
    ![18, 19].includes(paymentCode.length) ||
    !["1", "2", "5", "6"].includes(paymentCode[0])
  ) {
    return {
      success: false,
      message:
        paymentCode[0] === "6"
          ? "请输入19位长度的云闪付付款码!"
          : "请输入18位长度正确的付款码!"
    };
  }
  const isAli = str =>
    ["25", "26", "27", "28", "29", "30"].includes(str.substring(0, 2));
  const isWx = str => str[0] === "1" && str.length === 18;
  const isUnion = str => str[0] === "6" && str.length === 19;
  const isBest = str => str[0] === "5" && str.length === 18;

  const checkMap = [
    {
      label: "微信支付",
      key: "WeChatPay",
      checkKey: "isWeChatPayEnabled",
      check: isWx
    },
    {
      label: "支付宝支付",
      key: "AliPay",
      checkKey: "isAliPayEnabled",
      check: isAli
    },
    {
      label: "云闪付",
      key: "UnionPay",
      checkKey: "isUnionPayEnabled",
      check: isUnion
    },
    {
      label: "翼支付",
      key: "BestPay",
      checkKey: "isBestPayEnabled",
      check: isBest
    }
  ];

  while (checkMap.length) {
    const item = checkMap.shift();
    if (item.check(paymentCode)) {
      const enabled = global[`is${item.key}Enabled`];
      return {
        success: enabled,
        message: enabled ? "" : "没有开启" + item.label,
        thirdPayWay: item.key
      };
    }
  }

  return {
    success: false,
    message: "请输入正确的付款码"
  };
}
