<template>
  <el-dialog
    :visible.sync="show"
    title="选择员工"
    class="staff-picker-action"
    width="822px"
    @closed="close"
  >
    <div class="staff-picker">
      <el-row class="staff-list" :gutter="30">
        <el-col :span="8" v-for="item in options" :key="item.key">
          <div
            class="staff-item"
            :class="isPick(item) ? 'picked' : ''"
            @click="pickItem(item)"
          >
            {{ item.text }}
          </div>
        </el-col>
      </el-row>
    </div>
  </el-dialog>
</template>

<script>
const copy = (o) => JSON.parse(JSON.stringify(o));
export default {
  name: "staff-picker",
  data() {
    return {
      show: false,
      value: [],
      options: [],
    };
  },
  computed: {
    keys() {
      return this.value.map((it) => it.key);
    },
  },
  methods: {
    isPick(item) {
      return this.keys.includes(item.key);
    },
    pickItem(item) {
      if (!this.radio) console.log("暂未实现多选逻辑");
      this.value = [item];
      this.confirm();
    },
    close() {
      this.resolve({
        success: false,
        value: [],
      });
      this.$destroy();
    },
    confirm() {
      this.resolve({
        success: true,
        value: this.value,
      });
      this.show = false;
    },
    onCancel() {
      this.resolve({
        success: false,
        value: [],
      });
      this.show = false;
    },
  },
  watch: {},
  created() {},
  mounted() {},
  beforeDestroy() {
    this.$el && this.$el.remove();
  },
};
</script>

<style lang="less" rel="stylesheet/less">
@active-color: #3a7ddb;
.staff-picker-action {
  .el-dialog__header {
    height: 64px;
    background-color: @active-color;
    .el-dialog__title {
      color: #fff;
    }
    .el-icon-close {
      color: #fff;
    }
  }
  .el-dialog__body {
    padding: 0 30px;
  }
  .el-dialog {
    border-radius: 12px;
    overflow: hidden;
  }

  .staff-picker {
    height: 400px;
    overflow-y: auto;
    overflow-x: hidden;
    .staff-list {
      padding: 10px 0;
      .staff-item {
        padding: 10px 20px;
        font-size: 20px;
        margin: 10px 0;
        line-height: 32px;
        border: 1px solid #eaeaea;
        border-radius: 6px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        &.picked {
          border-color: @active-color;
          background-color: #f2f7ff;
        }
      }
    }
  }
}
</style>
