import axios from "axios";
import { Loading, Message } from "element-ui";

// region tools
const transform = data =>
  Object.keys(data)
    .reduce(
      (pre, key) =>
        `${pre}${encodeURIComponent(key)}=${encodeURIComponent(data[key])}&`,
      ""
    )
    .replace(/&$/, "");

const callLoading = () => {
  return Loading.service({ background: "rgba(255,255,255,.35)" });
};

const clearLoading = () => {
  return Loading.service({}).close();
};

const callToast = message => {
  return Message({
    type: message.type || "error",
    message: message.message || message
  });
};

const stringify = str => btoa(str);

// endregion

// region 拦截器
// 添加响应拦截器
axios.interceptors.response.use(
  function(response) {
    // 对响应数据做点什么
    if (typeof response.data.data === "string") {
      try {
        response.data.data = JSON.parse(response.data.data);
      } catch (e) {
        console.log("not json type");
      }
    }

    return response;
  },
  function(error) {
    // 对响应错误做点什么
    return Promise.reject(error);
  }
);

// endregion

// region modify
const getPath = url => (/^https?/.test(url) ? url : location.pathname + url);

const dftOptions = {
  isSuccess: response => response.success,
  loading: true,
  toast: true
};

const dftConfig = {
  method: "POST",
  data: {},
  headers: {
    "Content-Type": "application/x-www-form-urlencoded"
  },
  timeout: 30000
};

function handleOptions(options) {
  return {
    ...dftOptions,
    ...options
  };
}

function handleConfig(url, config) {
  config = { ...dftConfig, ...config };
  const params = config.params || null;

  return {
    url,
    params,
    data: config.data,
    method: config.method,
    headers: config.headers,
    timeout: config.timeout,
    transformRequest: [transform]
  };
}

// endregion

export default function request(url = "", config = {}, options = {}) {
  config = handleConfig(url, config);
  const { loading, isSuccess, toast } = handleOptions(options);
  loading && callLoading();
  return new Promise(resolve => {
    axios(config)
      .then(res => {
        loading && clearLoading();
        const response = res.data;
        const success = isSuccess(response);
        if (!success) {
          toast &&
            callToast({
              type: "error",
              message: response.message || "业务异常"
            });
        }
        resolve({ ...response, success, response: res });
      })
      // 网络异常及代码错误
      .catch(data => {
        loading && clearLoading();
        const { response } = data;
        const message = response.data.message || "网络故障";
        toast && callToast(message);
        resolve({
          success: false,
          message,
          data: response || data
        });
      });
  });
}

export async function cacheRequest(url, config, options) {
  const key = stringify(url + JSON.stringify(config.data));
  const cache = sessionStorage.getItem(key);
  if (cache) {
    return JSON.parse(cache);
  }
  const result = await request(url, config, options);
  if (result.success) {
    sessionStorage.setItem(key, JSON.stringify(result));
  }
  return result;
}
