<template>
  <el-popover
    :placement="position"
    width="200"
    v-model="show"
    @after-leave="close"
  >
    <div class="order-picker">
      <ul class="list">
        <li
          class="item"
          v-for="item in options"
          @click.stop="confirm(item)"
          :key="item.oilOrderGuid"
        >
          <span class="money">{{ item.money }}</span>
          <span class="time">{{ item.time | clock }}</span>
        </li>
        <li class="item manual" @click.stop="confirm(null)">手动输入金额</li>
      </ul>
    </div>
  </el-popover>
</template>

<script>
import * as utils from "@/utils";

export default {
  name: "order-picker",
  props: {},
  data() {
    return {
      show: false,
      options: [],
    };
  },
  filters: {
    clock: utils.fmt("HH:mm"),
  },
  computed: {},
  methods: {
    close() {
      this.$destroy();
    },
    confirm(item) {
      this.resolve({
        success: true,
        value: item,
      });
      this.show = false;
    },
    onCancel() {
      this.resolve({
        success: false,
        value: null,
      });
      this.show = false;
    },
  },
  watch: {},
  created() {
    console.log("show");
    this.onClickOtherWhere = () => {
      this.onCancel();
    };
    setTimeout(() => {
      document.body.addEventListener("click", this.onClickOtherWhere);
    }, 350);
  },
  mounted() {},
  beforeDestroy() {
    document.body.removeEventListener("click", this.onClickOtherWhere);
    this.$el && this.$el.remove();
  },
};
</script>

<style lang="less" rel="stylesheet/less">
.order-picker {
  max-height: 300px;
  overflow-y: auto;
  overflow-x: hidden;
  .list {
    list-style: none;
    margin: 0;
    padding: 0;
    .item {
      height: 48px;
      border: 1px solid #e5e5e5;
      background-color: #f8f8f8;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 10px;
      padding: 0 10px;
      border-radius: 4px;
      cursor: pointer;
      .money {
        color: #000;
        font-weight: bold;
        font-size: 18px;
        &:before {
          font-weight: normal;
          content: "￥";
          font-size: 14px;
        }
      }
      .time {
        color: #999;
      }
    }
    .manual {
      justify-content: center;
      color: #6596e1;
      font-size: 18px;
      font-weight: 500;
    }
  }
}
</style>
