import { mapState, mapGetters } from "vuex";
import * as API from "api";

export default {
  data() {
    return {};
  },
  methods: {
    async payOrder({ thirdPayType, dynamicId, paidThirdPay }) {
      const payload = {
        oilOrderGuid: this.noPayOrder?.oilOrderGuid,
        memberGuid: this.memberData?.memberGuid ?? null,
        totalMoney: +this.orderMoney,
        paidDiscount: +(this.discountMap.discount?.money ?? 0),
        paidFullCut: +(this.discountMap.activity?.money ?? 0),
        paidPoint: +(this.discountMap.point?.money ?? 0),
        paidValue: this.paidValue,
        paidCoupon: +(this.discountMap.coupon?.money ?? 0),
        paidOilDecrease: +(
          this.discountMap.value?.decreaseData?.paidOilDecrease ?? 0
        ),
        paidLockOilPrice: +(
          this.discountMap.value?.decreaseData?.paidLockOilPrice ?? 0
        ),
        changeMoney: this.changeMoney,
        depositedMoney: this.depositedMoney,
        preferentialMoney: this.preferentialMoney,
        paidMoney: this.paidMoney,
        paidCard: this.paidCard,
        paidOther: this.paidOther,
        otherPayType: this.otherPayType,
        paidThirdPay,
        thirdPayType,
        dynamicId,
        activityGuid: this.activity?.guid ?? null,
        meno: this.memo,
        valuePaidType: this.valuePaidType,
        paidValueMemberGuid: this.paidValueMemberGuid,
        password: this.isNeedValidPassword ? this.valueCheckCode : "",
        gasItem: this.reqGasItem,
        useCouponItem: this.discountMap.coupon?.coupons ?? null,
        valueGasDecreaseList:
          this.discountMap.value?.decreaseData?.valueGasDecreaseList ?? null,
        gunGuid: this.oilGunGuid,
        deductStaffGuids: this.$store.state.Main.pickedStaff
          .map((it) => it.staffGuid)
          .join(","),
      };

      console.log("----------支付参数如下-----------");
      console.table(payload);

      const result = await API.payOrder(JSON.stringify(payload));

      console.log(result);
      return result;
    },

    async getOrderStatus(orderNumber, loopCount) {
      let result = { success: true, message: "", data: { orderStatus: 0 } };
      try {
        while (
          (result.success && result.data.orderStatus === 0) ||
          result.data.orderStatus === 2
        ) {
          if (loopCount <= 0) {
            this.$message({
              type: "warning",
              message: "订单超时未完成支付",
            });
            break;
          }
          loopCount--;
          console.log("检查订单状态,剩余轮询次数", loopCount);
          result = await API.getOrderNumberStatus(orderNumber);
          if (
            !(result.data.orderStatus === 0 || result.data.orderStatus === 2)
          ) {
            break;
          }
          console.log("------->", result);
          await this.$utils.sleep(2000);
        }
        if (result.success && result.data.orderStatus === 1) {
          console.log("查单支付成功!");
          this.restore();
          this.paidData = result.data;
          this.resultShow = true;
          this.noticePrint(result.data);
        } else {
          console.log("查单!---", result);
          //clearLoading();
          if (
            result.success &&
            !(
              result.data.orderStatus === 1 ||
              result.data.orderStatus === 0 ||
              result.data.orderStatus === 2
            )
          ) {
            this.$message({
              type: "warning",
              message: result.message,
            });
          }
        }
      } catch (e) {
        this.$message({
          type: "warning",
          message: e,
        });
      }
    },
  },
  computed: {
    paidMoney() {
      if (this.pickedThirdPaytype !== "cash") return 0;
      return this.formatRestMoney;
    },
    paidValue() {
      const value = this.discountMap.value;
      if (!value) return 0;
      if (value.decreaseData) return value.decreaseData.paidValue;
      return +value.money;
    },
    paidCard() {
      if (this.pickedThirdPaytype !== "bank") return 0;
      return this.formatRestMoney;
    },
    otherPayType() {
      const other = this.thirdPaytype.find((it) => it.name === "other");
      if (!other) return "";
      const list = other.subOptions;
      return list.find((it) => it.name === this.pickedThirdPaytype)?.name ?? "";
    },
    paidOther() {
      const other = this.thirdPaytype.find((it) => it.name === "other");
      if (!other) return 0;
      const list = other.subOptions;
      return list.find((it) => it.name === this.pickedThirdPaytype)
        ? this.formatRestMoney
        : 0;
    },
    reqGasItem() {
      const goods = this.currentGasItem;
      if (!goods) return null;
      const price = goods.isBargain ? goods.bargainPrice : goods.price;
      const number = +Math.changeToDecimal(Math.divide(this.orderMoney, price));
      return {
        goodsItemGuid: goods.goodsItemGuid,
        number,
        price,
        discountRate: this.discountParams?.discountRate ?? 1, //--按金额折扣系数
        pointRate: this.discountParams?.pointRate ?? 0, //--积分抵扣系数
        discountPerLiter: this.discountParams?.discountPerLiter ?? 0, // 按升折扣系数
        pointPerLiter: this.discountParams?.pointPerLiter ?? 0, // 按升积分时，每升积分
        discountMode: this.discountParams?.discountMode ?? 0, // 折扣模式
        isBargain: goods.isBargain, // --是否特价
        allowBargainDiscount: goods.allowBargainDiscount, //--特价是否打折
        allowBargainPoint: goods.allowBargainPoint, //--特价是否积分
        pointDiscountRuleGuid:
          this.discountParams?.pointDiscountRuleGuid ?? null, //--折扣系数
      };
    },
    // 储值支付类型  0：默认、1：母卡、2：本卡(含自己是油卡)、3副卡。
    valuePaidType() {
      // 非会员 0
      const checkMap = this.discountCheckMap;
      if (!this.memberData) return 0;
      // 没有使用储值,为0
      if (!checkMap["value"] && !checkMap["motherCard"]) return 0;
      if (checkMap["motherCard"]) return 1;
      // 没有分油卡,必然是本卡
      if (!this.currentSubOilCard) return 2;
      // 当前选中的分油卡与会员卡一致,则是本卡
      if (this.currentSubOilCard.groupGuid === this.memberData.memberGroupGuid)
        return 2;
      return 3;
    },

    paidValueMemberGuid() {
      const checkMap = this.discountCheckMap;
      if (!checkMap["value"] && !checkMap["motherCard"]) return null;
      //母卡支付,返回母卡guid
      if (checkMap["motherCard"]) return this.motherCard.motherCardGuid;
      //油卡支付,返回油卡guid
      if (this.currentSubOilCard && this.currentSubOilCard.oilCardGuid)
        return this.currentSubOilCard.oilCardGuid;
      //普通支付,返回当前会员guid
      return this.memberData?.memberGuid ?? null;
    },

    // region 映射store
    ...mapGetters("Main", [
      "preferentialMoney",
      "activity",
      "currentGasItem",
      "motherCard",
      "currentSubOilCard",
    ]),
    ...mapState("Main", [
      "discountCheckMap",
      "discountParams",
      "discountMap",
      "noPayOrder",
      "oilGunGuid",
    ]),
    // endregion
  },
};
