<template>
  <el-popover
    class="description-pop"
    placement="top-start"
    :width="width"
    trigger="click"
    :title="title"
    :content="$slots.default ? undefined : message"
  >
    <slot></slot>
    <span slot="reference" @click.stop><svg-icon name="notice" /></span>
  </el-popover>
</template>

<script>
export default {
  name: "DescriptionPop",
  props: {
    title: {
      type: String,
      default: "",
    },
    message: String,
    width: {
      type: String,
      default: "250",
    },
  },
  components: {},
  data() {
    return {};
  },
  created() {},
  methods: {},
  computed: {},
};
</script>

<style lang="less" rel="stylesheet/less">
.description-pop {
}
</style>
